import './styles.css';

import * as React from 'react';

import { IRolesProps } from './RolesProps';

export default class Roles extends React.PureComponent<IRolesProps> {
    public render() {
        return <div className="table-responsive">
            <table className="table project-team-table table table-striped table-bordered">
                <thead>
                    <tr>
                        <th> Role </th>
                        <th> User </th>
                    </tr>
                </thead>
                <tbody>
                    {
                        this.props.roles.map(role => (
                            <tr key={role.id}>
                                <td>{role.name}</td>
                                <td>{role.usersStr}</td>
                            </tr>
                        ))
                    }
                </tbody>
            </table>
        </div>
    }
}




