import { Action, Reducer } from 'redux';

import ActionTypes from '../../actions/ActionTypes';
import * as Actions from '../../actions/Common/IActions';
import CommonState from '../../store/Common/CommonState';

const initialState = {
    allOffices: [],
    
    allUsers: [],
    allUsersRequest: false,
    allUsersRequestError: false,

    activeProjects: [],
    activeProjectsRequest: false,
    activeProjectsRequestError: false,

    projectTemplates: [],
    projectTemplatesRequest: false,
    projectTemplatesRequestError: false,

    loggedUser: null
}

export const CommonReducer: Reducer<CommonState> = (state: CommonState = initialState, incomingAction: Action) => {
    const action = incomingAction as Actions.AllUsersListAction
        | Actions.IUserLoggedInAction
        | Actions.ActiveProjectsAction
        | Actions.ProjectTemplatesAction;

    switch (action.type) {
        case ActionTypes.REQUEST_ALL_USERS_LIST:
            {
                return {
                    ...state,
                    allUsersRequest: true
                };
            }
        case ActionTypes.ALL_USERS_LIST_REQUEST_ERROR:
            {
                return {
                    ...state,
                    allUsersRequest: false,
                    allUsersRequestError: true
                };
            }
        case ActionTypes.RECEIVE_ALL_USERS_LIST:
            {
                return {
                    ...state,
                    allOffices: action.offices,
                    allUsers: action.users,
                    allUsersRequest: false,
                    allUsersRequestError: false                
                };
            }
        case ActionTypes.REQUEST_PROJECT_TEMPLATES:
            {
                return {
                    ...state,
                    projectTemplatesRequest: true
                };
            }
        case ActionTypes.PROJECT_TEMPLATES_REQUEST_ERROR:
            {
                return {
                    ...state,
                    projectTemplatesRequest: false,
                    projectTemplatesRequestError: true
                };
            }
        case ActionTypes.RECEIVE_PROJECT_TEMPLATES:
            {
                return {
                    ...state,
                    projectTemplates: action.projectTemplates,
                    projectTemplatesRequest: false,
                    projectTemplatesRequestError: false                    
                };
            }
        case ActionTypes.REQUEST_ACTIVE_PROJECTS:
            {
                return {
                    ...state,
                    activeProjectsRequest: true
                };
            }
        case ActionTypes.ACTIVE_PROJECTS_REQUEST_ERROR:
            {
                return {
                    ...state,
                    activeProjectsRequest: false,
                    activeProjectsRequestError: true
                };
            }
        case ActionTypes.RECEIVE_ACTIVE_PROJECTS:
            {
                return {
                    ...state,        
                    activeProjects: action.projects,            
                    activeProjectsRequest: false,
                    activeProjectsRequestError: false                    
                };
            }
        case ActionTypes.USER_LOGGED_IN:
            {
                return {
                    ...state,
                    loggedUser: action.user
                };
            }
        default: {
            return state;
        }
    }
};


