import * as moment from 'moment';

import Consts from '../Consts';
import SPLookupValue from './SPLookupValue';

export default class Task {
    public id: number = 0;

    public assignedTo: SPLookupValue[] = [];
    public assignedBy: SPLookupValue | null = null;

    public name: string = '';
    public status: string = '';
    public due: string = '';
    public phase: string = '';

    public projectSiteUrl: string = '';
    public deltekProjectNumber: string = '';

    public templateLinkUrl: string = '';
    public deliverableLinkUrl: string = '';
    public videoLinkUrl: string = '';

    public deliverable: string = '';
    public teamMember: string = '';
    public milestone: string = '';

    public get assignedToStr(): string {
        return this.assignedTo.map(u => u.displayName).join(', ');
    }

    public get assignedByStr(): string {
        return this.assignedBy !== null ? this.assignedBy.displayName : '';
    }

    public get dueLocal(): moment.Moment {
        const dueDateUtc = moment.utc(this.due);
        return dueDateUtc.local();
    }

    public get dueLocalStr(): string {
        const dueLocal = this.dueLocal;
        if (dueLocal.isValid()) {
            return dueLocal.format(Consts.DateTimeFormat);
        }
        return '';
    }

    public get overdue(): boolean {
        const dueDateUtc = moment.utc(this.due);
        const currentUtcDate = moment.utc();

        if (dueDateUtc.isValid()) {
            return dueDateUtc.isBefore(currentUtcDate);
        }

        return false;
    }
}


