import { Logger } from '@pnp/logging';

import api from '../../api/api';
import Consts from '../../Consts';
import Milestone from '../../models/Milestone';
import Project from '../../models/Project';
import Role from '../../models/Role';
import SPLookupValue from '../../models/SPLookupValue';
import Task from '../../models/Task';
import TaskTemplate from '../../models/TaskTemplate';
import { AppThunkAction, IApplicationState } from '../../store';
import ActionTypes from '../ActionTypes';
import AddTasksActionsCreatorBase from '../AddTasksActionsCreatorBase';
import { IProjectsDashboardTasksUpdatedAction } from '../ProjectsDashboard/IActions';
import { ICloseDialogAction } from '../RootModal/IActions';
import { ITeamDashboardTasksUpdatedAction } from '../TeamDashboard/IActions';
import { AddModulesAction, ProjectTemplateDataAction } from './IActions';

type AddModulesDispatchType =
    IProjectsDashboardTasksUpdatedAction
    | ITeamDashboardTasksUpdatedAction
    | AddModulesAction
    | ICloseDialogAction;

class ActionCreators extends AddTasksActionsCreatorBase {

    public AddModules(addModulesFormData: any): AppThunkAction<AddModulesDispatchType> {
        return (dispatch: any, getState: any) => {
            const state = (getState() as IApplicationState);
            const addModulesDialogState = state.addModulesFormState;

            const taskTemplatesToAdd = addModulesDialogState.projectTemplateTasks
                .filter(t => addModulesFormData.modules
                    .indexOf(t.milestone) >= 0);

            const project = state.common.activeProjects
                .find(p => p.deltekProjectNumber === addModulesFormData.deltekProjectNumber) as Project;

            api.Roles(project.deltekProjectNumber)
                .then(roles => {
                    
                    const tasksToAdd = this.ToTasks(taskTemplatesToAdd, roles, addModulesFormData, state.common.loggedUser);
                    const addTasksPromise = api.AddTasks(addModulesFormData.deltekProjectNumber, ...tasksToAdd);

                    const milestonesToAdd = this.ToMilestones(addModulesFormData);
                    const addMilestonesPromise = api.AddMilestones(project, milestonesToAdd);

                    Promise.all([addTasksPromise, addMilestonesPromise])
                        .then((results: [Task[], Milestone[]]) => {
                            const tasks = results[0];

                            dispatch({
                                type: ActionTypes.ADD_MODUES_REQUEST_SUCCESS
                            });

                            this.CloseModalDialog(dispatch);
                            this.TryUpdateProjectsDashboardTasks(dispatch, state.projectsDashboard, ...tasks);
                            this.TryUpdateTeamDashboardTasks(dispatch, state.teamDashboard, ...tasks);
                        });
                })
                .catch(error => {
                    Logger.error(new Error('Add modules request error'))
                    dispatch({
                        type: ActionTypes.ADD_MODUES_REQUEST_ERROR
                    });
                });
            dispatch({
                type: ActionTypes.ADD_MODUES_REQUEST
            });
        }
    }

    public ProjectTemplateSelected(projectTemplateUrl: string): AppThunkAction<ProjectTemplateDataAction> {
        return ((dispatch: any, getState: any) => {
            if (!projectTemplateUrl || projectTemplateUrl === '') {
                this.ResetProjectTemplateDate(dispatch);
            } else {
                this.RequestProjectTemplateData(dispatch, projectTemplateUrl);
            }
        }).bind(this);
    }
    private CloseModalDialog(dispatch: any) {
        dispatch({
            type: ActionTypes.CLOSE_MODAL_DIALOG
        });
    }

    private ToTasks(taskTemplates: TaskTemplate[], roles: Role[], addModulesFormData: any, assignedBy: SPLookupValue | null): Task[] {
        return taskTemplates.map(t => {
            const task = new Task();
            const assignedTo = roles.find(r => r.name === t.teamMember);
            if (assignedTo) {
                task.assignedTo = assignedTo.users;
            }
            task.name = t.title;
            task.deliverable = t.deliverable
            task.milestone = t.milestone;
            task.due = addModulesFormData.dueDateUtc;
            task.phase = addModulesFormData.phase;
            task.status = Consts.TaskStatuces.NotStarted;
            task.assignedBy = assignedBy;

            return task;
        });
    }

    private ToMilestones(addModulesFormData: any): Milestone[] {
        return addModulesFormData.modules.map((m: string) => {
            const milestone = new Milestone();
            milestone.eventDate = addModulesFormData.dueDateUtc;
            milestone.title = m;

            return milestone;
        });
    }

    private ResetProjectTemplateDate(dispatch: any) {
        dispatch({
            type: ActionTypes.RESET_PROJECT_TEMPLATE_DATA
        });
    }

    private RequestProjectTemplateData(dispatch: any, projectTemplateUrl: string) {
        api.TaskTemplates(projectTemplateUrl)
            .then((taskTemplates: TaskTemplate[]) => {
                dispatch({
                    taskTemplates,
                    type: ActionTypes.RECEIVE_PROJECT_TEMPLATE_DATA
                });
            })
            .catch(error => {
                Logger.error(error);
                dispatch({
                    type: ActionTypes.PROJECT_TEMPLATE_DATA_REQUEST_ERROR
                });
            });
        dispatch({
            type: ActionTypes.REQUEST_PROJECT_TEMPLATE_DATA
        });
    }
}

const actionCreators = new ActionCreators();
export default actionCreators;



