import { AppThunkAction } from '../../store';
import ActionTypes from '../ActionTypes';
import { ICloseDialogAction, IOpenDialogAction } from './IActions';

class ActionCreators {
    public OpenModalDialog(modalType: string, modalProps: any, modalTitle: string): AppThunkAction<IOpenDialogAction> {
        return ((dispatch) => {
            dispatch({
                modalProps,
                modalTitle,
                modalType,
                type: ActionTypes.OPEN_MODAL_DIALOG                
            });
        })
    }

    public CloseModalDialog(): AppThunkAction<ICloseDialogAction> {
        return ((dispatch) => {
            dispatch({
                type: ActionTypes.CLOSE_MODAL_DIALOG,
            });
        });
    }
}

const actionCreators = new ActionCreators();
export default actionCreators;


