import * as React from 'react';
import { Button, ButtonToolbar } from 'react-bootstrap';

import { ITaskLinksProps } from './TaskLinksProps';

export default class TaskLinks extends React.PureComponent<ITaskLinksProps> {
    constructor(props: any) {
        super(props);
        this.renderLink = this.renderLink.bind(this);

        this.state = { editMode: false }
    }

    public renderLink(link: string, title: string) {
        const set = link !== null && link !== '';
        const className = !set ? 'hidden' : '';
        return <Button bsSize="xsmall" target="_blank" href={link} className={className} bsStyle="primary">{title}</Button>
    }

    public render() {
        const templateLink = this.renderLink(this.props.task.templateLinkUrl, "Template");
        const deliverableLink = this.renderLink(this.props.task.deliverableLinkUrl, "Deliverable");
        const videoLink = this.renderLink(this.props.task.videoLinkUrl, "Video");

        return <ButtonToolbar>
            {templateLink}
            {deliverableLink}
            {videoLink}
        </ButtonToolbar>
    }
}


