import { SiteUserProps } from '@pnp/sp';

import Milestone from '../models/Milestone';
import Project from '../models/Project';
import ProjectTemplate from '../models/ProjectTemplate';
import Role from '../models/Role';
import SPUser from '../models/SPUser';
import Task from '../models/Task';
import TaskTemplate from '../models/TaskTemplate';
import MilestonesRepo from '../repo/MilestonesRepo';
import ProjectsRepo from '../repo/ProjectsRepo';
import ProjectTemplatesRepo from '../repo/ProjectTemplatesRepo';
import RolesRepo from '../repo/RolesRepo';
import TasksRepo from '../repo/TasksRepo';
import TaskTemplatesRepo from '../repo/TaskTemplatesRepo';
import UsersRepo from '../repo/UsersRepo';

class Api {
    private usersRepo: UsersRepo = new UsersRepo();
    private projectTemplatesRepo = new ProjectTemplatesRepo();
    private projectsRepo = new ProjectsRepo();
    private tasksRepo = new TasksRepo();
    private rolesRepo = new RolesRepo();
    private taskTemplatesRepo = new TaskTemplatesRepo();
    private milestonesRepo = new MilestonesRepo();

    public TasksRelatedToUser(deltekProjectNumber: string, userId: number, limit: number = 0): Promise<Task[]> {
        return this.tasksRepo
            .GetTasksRelatedToUser(deltekProjectNumber, userId, limit)
    }

    public ActiveTasksByProject(deltekProjectNumber: string): Promise<Task[]> {
        return this.tasksRepo
            .GetActiveTasksByProject(deltekProjectNumber)
    }

    public ActiveTasksByProjectAndUser(deltekProjectNumber: string, userId: number): Promise<Task[]> {
        return this.tasksRepo
            .GetActiveTasksByProjectAndUser(deltekProjectNumber, userId)
    }

    public TasksAssignedToUsers(deltekProjectNumber: string, userIds: number[]): Promise<Task[]> {
        return this.tasksRepo
            .GetTasksAssignedToUsers(deltekProjectNumber, userIds);
    }

    public UpdateTask(task: Task): Promise<Task> {
        return this.tasksRepo
            .Update(task);
    }

    public AddTasks(deltekProjectNumber: string, ...tasks: Task[]): Promise<Task[]> {
        return this.tasksRepo
            .Add(deltekProjectNumber, tasks);
    }

    public ActiveProjects(): Promise<Project[]> {
        return this.projectsRepo
            .GetAll();
    }

    public Users(): Promise<SPUser[]> {
        return this.usersRepo
            .GetAll();
    }

    public ProjectTemplates(): Promise<ProjectTemplate[]> {
        return this.projectTemplatesRepo
            .GetAll();
    }

    public Roles(deltekProjectNumber: string): Promise<Role[]> {
        return this.rolesRepo
            .GetAll(deltekProjectNumber);
    }

    public TaskTemplates(projectTemplateUrl: string): Promise<TaskTemplate[]> {
        return this.taskTemplatesRepo
            .GetAll(projectTemplateUrl)
    }

    public AddMilestones(project: Project, milestones: Milestone[]): Promise<Milestone[]> {
        return this.milestonesRepo
            .Add(project, milestones);
    }

    public MilestonesByProject(project: Project): Promise<Milestone[]> {
        return this.milestonesRepo
            .GetAll(project);
    }

    public GetCurrentUser(): Promise<SiteUserProps> {
        return this.usersRepo
            .GetCurrentUser();
    }
}

const api = new Api();
export default api;


