import * as React from 'react';
import { Nav, Navbar, NavItem } from 'react-bootstrap';
import { Link, NavLink } from 'react-router-dom';
import LoggedUserContainer from '../containers/Common/LoggedUserContainer';
import LogOutContainer from '../containers/Common/LogOutContainer';

export class NavMenu extends React.Component<{}, {}> {
    public render() {
        return <Navbar className='main-nav' inverse={true} collapseOnSelect={true}>
        <Navbar.Header>
            <Navbar.Brand>
                <Link className='navbar-brand' to={ '/' }>WB.Dashboards</Link>
            </Navbar.Brand>
            <Navbar.Toggle />
        </Navbar.Header>
        <Navbar.Collapse>
            <ul className='nav navbar-nav'>
                <li>
                    <NavLink exact={true} to={ '/' } activeClassName='active'>
                        <span className='glyphicon glyphicon-tasks'/> Projects Dashboard
                    </NavLink>
                </li>
                <li>
                    <NavLink to={ '/team' } activeClassName='active'>
                        <span className='glyphicon glyphicon-user'/> Team Dashboard
                    </NavLink>
                </li>
                <li>
                    <NavLink to={ '/resources' } activeClassName='active'>
                        <span className='glyphicon glyphicon-signal'/> Resource Planning
                    </NavLink>
                </li>
            </ul>
            <Nav pullRight={true}>
                <NavItem>
                    <LoggedUserContainer />
                </NavItem>
                <NavItem className="pull-right">
                    <LogOutContainer />
                </NavItem>
            </Nav>
        </Navbar.Collapse>
      </Navbar>;
    }
}


