import Consts from '../Consts';
import ProjectTemplate from '../models/ProjectTemplate';
import Repo from './Repo';

export default class ProjectTemplatesRepo extends Repo {
    private consts = Consts.ProjectTemplatesListConsts;

    public GetAll(): Promise<ProjectTemplate[]> {
        const fields = [
            this.consts.Fields.TitleFieldName,
            this.consts.Fields.UrlFieldName,
            'Id'
        ].join(',');

        return this.RootWeb.lists.getByTitle(this.consts.ListTitle).items
            .select(fields)
            .getAll()
            .then(items => items.map(i => this.ToTemplate(i)));
    }

    private ToTemplate(item: any) {
        const projectTemplate: ProjectTemplate = new ProjectTemplate();
        projectTemplate.id = item.Id;
        projectTemplate.title = item[this.consts.Fields.TitleFieldName];
        projectTemplate.url = this.ToUrl(item[this.consts.Fields.UrlFieldName]);

        return projectTemplate;
    }
}


