import { connect } from 'react-redux';

import actionCreators from '../../actions/ProjectsDashboard/ActionCreators';
import ProjectsSearchableList from '../../components/Projects/ProjectsSearchableList/ProjectsSearchableList';
import { IApplicationState } from '../../store';

const mapStateToProps = (state: IApplicationState) => {
    return {
        projects: state.projectsDashboard.projects,
        selectedProject: state.projectsDashboard.project
    }
}

export default connect(
    mapStateToProps,
    {
        onProjectSelected: actionCreators.ProjectSelected.bind(actionCreators)
    })(ProjectsSearchableList);



