import * as React from 'react';
import SPUser from '../../../models/SPUser';
import SearchableList from '../../SearchableList/SearchableList';
import SPUserInfo from '../SPUserInfo/SPUserInfo';
import { IUsersSearchableListProps } from './UsersSearchableListProps';

export default class UsersSearchableList extends React.PureComponent<IUsersSearchableListProps> {
    constructor(props: IUsersSearchableListProps) {
        super(props);
        this.valueExtractor = this.valueExtractor.bind(this);
        this.match = this.match.bind(this);
    }

    public render() {
        type SearchableUsersListType = new () => SearchableList<SPUser>;
        const SearchableUsersList = SearchableList as SearchableUsersListType;

        return <SearchableUsersList
            items={this.props.users}
            selectedItem={this.props.selectedUser}
            onItemSelected={this.props.onUserSelected}
            match={this.match}
            valueExtractor={this.valueExtractor}
            renderItem={SPUserInfo}
            title="Users" />
    }

    public valueExtractor(user: SPUser) {
        return user.id.toString();
    }

    public match(request: string, project: SPUser): boolean {
        return project.displayName.indexOf(request) >= 0 ||
               project.jobTitle.indexOf(request) >= 0 ||
               project.office.indexOf(request) >= 0;
    }
}


