import * as React from 'react';

import { IProjectShortInfoProps } from './ProjectShortInfoProps';

const ProjectShortInfo: React.SFC<IProjectShortInfoProps> = (props: IProjectShortInfoProps) => {
    return <div>
    <h5 className="mb-1">
        {props.item.projectName}
    </h5>
    <h6 className="mb-1">
        <strong>{props.item.deltekProjectNumber} </strong>
    </h6>
</div>;
}
export default ProjectShortInfo;



