import * as React from 'react';
import { ListGroupItem } from 'react-bootstrap';

import { ITaskViewProps } from '../ITaskViewProps';
import TaskEditLinkTitle from '../TaskEditLinkTitle/TaskEditLinkTitle';
import TaskLinks from '../TaskLinks/TaskLinks';

const TaskMobile: React.SFC<ITaskViewProps> = (props: ITaskViewProps) => {
    return <ListGroupItem key={props.task.id}>
            <TaskEditLinkTitle
                task={props.task}
                onClick={props.onEdit} />

            <a href={props.task.projectSiteUrl}
                target="_blank"
                className="btn-link">{props.task.deltekProjectNumber}</a>
                <br />
                <br />
            <strong>Assigned To: </strong>{props.task.assignedToStr}
                <br />
            <strong>Assigned By: </strong>{props.task.assignedByStr}
                <br />
            <strong>Status: </strong>{props.task.status}
                <br />
            <strong>Due: </strong>{props.task.dueLocalStr}
                <br /><br />
            <TaskLinks task={props.task} />
        </ListGroupItem>
}

export default TaskMobile;




