import Consts from '../Consts';
import Milestone from '../models/Milestone';
import Project from '../models/Project';
import SPLookupValue from '../models/SPLookupValue';
import Repo from './Repo';

export default class MilestonesRepo extends Repo {
    private consts = Consts.MilestonesListConsts;

    private fields = [
        this.consts.Fields.EndDateFieldName,
        this.consts.Fields.StartDateFieldName,
        this.consts.Fields.TitleFieldName,
        'Id'
    ]

    public Add(project: Project, milestones: Milestone[]): Promise<Milestone[]> {
        return new Promise((resolve, reject) => {
            const web = this.GetProjectWeb(project.deltekProjectNumber);
            const list = web.lists.getByTitle(this.consts.ListTitle);

            list.getListItemEntityTypeFullName().then(entityTypeFullName => {

                const addItemsBatch = web.createBatch();
                const itemsToAdd = milestones
                    .map(m => this.ToListItem(m));

                const addedItems: any[] = [];

                itemsToAdd.forEach(item => {
                    list.items.inBatch(addItemsBatch)
                        .add(item, entityTypeFullName)
                        .then(r => addedItems.push(r.data))
                        .catch(reject);
                });

                addItemsBatch.execute()
                    .then(() => addedItems.map(i => this.ToMilestone(i, project.deltekProjectNumber, project.projectManager)))
                    .then(addedMilestones => resolve(addedMilestones));
            });
        });
    }

    public GetAll(project: Project): Promise<Milestone[]> {
        const web = this.GetProjectWeb(project.deltekProjectNumber);
        return web.lists.getByTitle(this.consts.ListTitle).items
            .select(this.fields.join(','))
            .getAll()
            .then(items => items.map(i => this.ToMilestone(i, project.deltekProjectNumber, project.projectManager)));
    }

    private ToListItem(milestone: Milestone): any {
        const listItem: any = {};
        listItem[this.consts.Fields.TitleFieldName] = milestone.title;
        listItem[this.consts.Fields.EndDateFieldName] = this.ToUtcFormat(milestone.eventDate);
        listItem[this.consts.Fields.StartDateFieldName] = this.ToUtcFormat(milestone.eventDate);

        return listItem;
    }

    private ToMilestone(listItem: any, deltekProjectNumber: string, projectManager: SPLookupValue | null): Milestone {
        const milestone: Milestone = new Milestone();
        const projectSiteUrl = this.ToProjectSiteUrl(deltekProjectNumber);

        milestone.deltekProjectNumber = deltekProjectNumber;
        milestone.id = listItem.id;
        milestone.title = listItem[this.consts.Fields.TitleFieldName];
        milestone.titleUrl = `${projectSiteUrl}/Lists/Calendar/DispForm.aspx?ID=${listItem.Id}`;
        milestone.eventDate = listItem[this.consts.Fields.EndDateFieldName];
        milestone.responsible = projectManager;
        milestone.projectSiteUrl = projectSiteUrl;

        return milestone;
    }
}


