import { Logger } from '@pnp/logging';

import api from '../../api/api';
import ProjectTemplate from '../../models/ProjectTemplate';
import SPLookupValue from '../../models/SPLookupValue';
import ActionTypes from '../ActionTypes';
import * as Actions from './IActions';

class ActionCreators {

    public AppStarted() {
        return ((dispatch: any, getState: any) => {
            this.RequestLoggedUserData(dispatch);
            this.RequestAllUsers(dispatch);
            this.RequestProjectTemplates(dispatch);
            this.RequestActiveProjects(dispatch);
        });
    }

    private RequestLoggedUserData(dispatch: (action: Actions.IUserLoggedInAction) => any) {
        api.GetCurrentUser().then(user => {
            const currentUser = new SPLookupValue();

            currentUser.id = user.Id;
            currentUser.displayName = user.Title;

            dispatch(
                {
                    type: ActionTypes.USER_LOGGED_IN,
                    user: currentUser
                });
        });
    }

    private RequestAllUsers(dispatch: (action: Actions.AllUsersListAction) => any) {
        api.Users()
            .then(users => {
                dispatch({
                    offices: Array.from(new Set(users.map(u => u.office))),
                    type: ActionTypes.RECEIVE_ALL_USERS_LIST,
                    users
                });
            })
            .catch(error => {
                Logger.error(error);
                dispatch({ 
                    type: ActionTypes.ALL_USERS_LIST_REQUEST_ERROR
                });
            });
        
        dispatch({
            type: ActionTypes.REQUEST_ALL_USERS_LIST
        });
    }

    private RequestProjectTemplates(dispatch: (action: Actions.ProjectTemplatesAction) => any) {
        api.ProjectTemplates()
            .then((projectTemplates: ProjectTemplate[]) => {
                dispatch({
                    projectTemplates,
                    type: ActionTypes.RECEIVE_PROJECT_TEMPLATES
                });
            })
            .catch(error => {
                Logger.error(error);
                dispatch({
                    type: ActionTypes.PROJECT_TEMPLATES_REQUEST_ERROR
                });
            });

        dispatch({
            type: ActionTypes.REQUEST_PROJECT_TEMPLATES
        });
    }

    private RequestActiveProjects(dispatch: (action: Actions.ActiveProjectsAction) => any) {
        api.ActiveProjects()
            .then(projects => {
                dispatch({
                    projects,
                    type: ActionTypes.RECEIVE_ACTIVE_PROJECTS
                });
            })
            .catch(error => {
                Logger.error(error);
                dispatch({
                    type: ActionTypes.ACTIVE_PROJECTS_REQUEST_ERROR
                });
            });
        
        dispatch({
            type: ActionTypes.REQUEST_ACTIVE_PROJECTS
        });
    } 
};

const actionCreators = new ActionCreators();
export default actionCreators;


