import * as moment from 'moment';
import * as React from 'react';
import {
    Button, ButtonToolbar, Checkbox, ControlLabel, Form, FormControl, FormGroup
} from 'react-bootstrap';

import Consts from '../../Consts';
import Project from '../../models/Project';
import DateTime from '../DateTime/DateTime';
import { IAddSingleTaskFormProps } from './AddSingleTaskFormProps';
import { IAddSingleTaskFormState } from './AddSingleTaskFormState';

export default class AddSingleTaskForm extends React.PureComponent<IAddSingleTaskFormProps, IAddSingleTaskFormState> {
    constructor(props: IAddSingleTaskFormProps) {
        super(props);

        let phase = '';
        let phases: string[] = [];
        let deltekProjectNumber = '';
        const project = props.project;

        if (project !== null) {
            deltekProjectNumber = project.deltekProjectNumber;
            if (project.phases.length > 0) {
                phase = project.phases[0].displayName;
                phases = project.phases
                    .map(p => p.displayName);
            }
        }

        this.state = {
            assignedToRoleName: '',
            associatedToMilestone: true,
            deltekProjectNumber,
            dueDateUtc: moment().utc().format(Consts.DateTimeFormat),
            dueDateValid: true,
            phase,
            phases,
            taskName: ''
        }

        this.associatedToMilestoneChanged = this.associatedToMilestoneChanged.bind(this);
        this.onProjectPhaseSelected = this.onProjectPhaseSelected.bind(this);
        this.onProjectSelected = this.onProjectSelected.bind(this);
        this.onDueDateUpdated = this.onDueDateUpdated.bind(this);
        this.onAddSingleTask = this.onAddSingleTask.bind(this);
        this.onTaskNameUpdated = this.onTaskNameUpdated.bind(this);
        this.onAssignedToUpdated = this.onAssignedToUpdated.bind(this);
    }

    public onDueDateUpdated(newDate: string, isValid: boolean) {
        this.setState(
            {
                ...this.state,
                dueDateUtc: newDate,
                dueDateValid: isValid
            });
    }

    public componentDidMount() {
        if (this.state.deltekProjectNumber !== '') {
            this.props.onProjectSelected(this.state.deltekProjectNumber);
        }
    }

    public associatedToMilestoneChanged() {
        this.setState(
            {
                ...this.state,
                associatedToMilestone: !this.state.associatedToMilestone
            });
    }

    public onProjectSelected(e: any) {
        const deltekProjectNumber = e.target.value;
        let phases: string[] = [];
        let phase: string = '';

        if (deltekProjectNumber !== '') {
            const project = this.props.projects
                .find(p => p.deltekProjectNumber === deltekProjectNumber);

            if (project) {
                phases = (project as Project).phases
                    .map(p => p.displayName);

                if (phases.length > 0) {
                    phase = phases[0];
                }
            }
        }

        this.setState(
            {
                ...this.state,
                deltekProjectNumber,
                phase,
                phases
            });

        this.props.onProjectSelected(deltekProjectNumber);
    }

    public onProjectPhaseSelected(e: any) {
        const phase = e.target.value;
        this.setState(
            {
                ...this.state,
                phase
            });
    }

    public onTaskNameUpdated(e: any) {
        const taskName = e.target.value;
        this.setState(
            {
                ...this.state,
                taskName
            });
    }

    public onAssignedToUpdated(e: any) {
        const assignedToRoleName = e.target.value;
        this.setState(
            {
                ...this.state,
                assignedToRoleName
            });
    }

    get valid(): boolean {
        return this.state.dueDateValid
            && this.state.deltekProjectNumber !== ''
            && this.state.assignedToRoleName !== ''
            && this.state.taskName !== '';
    }

    public onAddSingleTask() {
        if (this.valid) {
            const formData = {
                assignedToRoleName: this.state.assignedToRoleName,
                deltekProjectNumber: this.state.deltekProjectNumber,
                dueDateUtc: this.state.dueDateUtc,
                phase: this.state.associatedToMilestone ? this.state.phase : '',
                taskName: this.state.taskName
            };
            this.props.onAddSingleTask(formData);
        }
    }

    public render() {
        return <Form>
            <FormGroup validationState={this.state.deltekProjectNumber === '' ? 'error' : null}>
                <ControlLabel>
                    Select a Project
                </ControlLabel>
                <FormControl componentClass="select"
                    defaultValue={this.state.deltekProjectNumber}
                    onChange={this.onProjectSelected}
                    placeholder="select">
                    <option />
                    {this.props.projects.map(p =>
                            <option key={p.id} value={p.deltekProjectNumber}>{p.projectName}</option>)}
                </FormControl>
            </FormGroup>
            <FormGroup>
                <ControlLabel>
                    <Checkbox
                        inline={true}
                        checked={this.state.associatedToMilestone}
                        onChange={this.associatedToMilestoneChanged}>
                        <strong>Associated to Milestone</strong>
                    </Checkbox>
                </ControlLabel>
                <FormControl componentClass="select"
                    disabled={!this.state.associatedToMilestone}
                    onChange={this.onProjectPhaseSelected}
                    placeholder="select">
                    {this.state.phases
                        .map((p, i) => <option key={i} value={p}>{p}</option>)}
                </FormControl>
            </FormGroup>
            <FormGroup validationState={this.state.taskName === '' ? 'error' : null}>
                <ControlLabel>
                    Task Name
                </ControlLabel>
                <FormControl componentClass="input"
                    onChange={this.onTaskNameUpdated}
                    placeholder="Please provide"/>
            </FormGroup>
            <FormGroup validationState={this.state.assignedToRoleName === '' ? 'error' : null}>
                <ControlLabel>
                    Assigned To
                </ControlLabel>
                <FormControl componentClass="select"
                    onChange={this.onAssignedToUpdated}>
                    <option />
                    {this.props.roles.map(r =>
                            <option key={r.id} value={r.name}>
                                {r.name} {r.usersStr !== '' ? `(${r.usersStr})` : ''}
                            </option>)}
                </FormControl>
            </FormGroup>
            <FormGroup validationState={this.state.dueDateValid ? null : 'error'}>
                <ControlLabel>Due Date</ControlLabel>
                <DateTime
                    onUpdated={this.onDueDateUpdated}
                    initialDate={this.state.dueDateUtc}
                    timeFormat={Consts.TimeFormat}
                    dateFormat={Consts.DateFormat} />
            </FormGroup>
            <FormGroup>
                <ButtonToolbar>
                    <Button
                        className="pull-right"
                        disabled={!this.valid}
                        onClick={this.onAddSingleTask}
                        bsStyle="success">
                        Add Single Task
                    </Button>
                </ButtonToolbar>
            </FormGroup>
        </Form>;
    }
}


