import { SiteUserProps } from '@pnp/sp/src/siteusers';

import Consts from '../Consts';
import SPUser from '../models/SPUser';
import Repo from './Repo';

export default class UsersRepo extends Repo {
    private consts = Consts.UsersListConsts;

    public GetAll(): Promise<SPUser[]> {
        const fields = [
            this.consts.Fields.NameFieldName,
            this.consts.Fields.JobTitleFieldName,
            this.consts.Fields.OfficeFieldName,
            'Id'
        ].join(',');

        const jobTitleFieldFilter = `${this.consts.Fields.JobTitleFieldName} ne null`;
        const officeFieldFilter = `${this.consts.Fields.OfficeFieldName} ne null`;
        const filter = `(${jobTitleFieldFilter}) and (${officeFieldFilter})`;
        
        return this.RootWeb.siteUserInfoList.items
            .select(fields)
            .filter(filter)
            .orderBy(this.consts.Fields.NameFieldName)
            .get()
            .then(items => items.map(i => this.ToUser(i)));
    }

    public GetCurrentUser(): Promise<SiteUserProps> {
        return this.RootWeb.currentUser.get();
    }

    private ToUser(item: any) {
        const user: SPUser = new SPUser();
        user.id = item.Id;
        user.displayName = item[this.consts.Fields.NameFieldName];
        user.jobTitle = item.JobTitle;
        user.office = item.Office;

        return user;
    }
}


