import * as React from 'react';
import { Button, Modal } from 'react-bootstrap';

interface IModalProps {
    title: string, 
    hideCloseBtn?: boolean
}

export default function modal<TProps>(Component: React.ComponentType<TProps>, modalProps: IModalProps, onClose: () => void = ()=>{;}) {    
    return class extends React.Component<TProps, {visible: boolean}> {
        constructor(props: TProps) {
            super(props);
            this.state = {visible: true};
            this.onClose = this.onClose.bind(this);
        }

        public render() {
            return <Modal backdrop="static" show={this.state.visible} onHide={this.onClose}>
                <Modal.Header>
                    <Modal.Title>
                        {modalProps.title}
                        {
                            modalProps.hideCloseBtn ? null : 
                                <Button className="pull-right" bsStyle="warning" onClick={this.onClose}>Close</Button>
                        }
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Component {...this.props}/>
                </Modal.Body>
            </Modal>
        }

        private onClose() {
            this.setState({
                visible: false
            });
            onClose();
        }
    };
}


