import { Logger } from '@pnp/logging';

import api from '../../api/api';
import Milestone from '../../models/Milestone';
import Project from '../../models/Project';
import SPUser from '../../models/SPUser';
import Task from '../../models/Task';
import { IApplicationState } from '../../store';
import ActionTypes from '../ActionTypes';
import * as Actions from './IActions';

class ActionCreators {

    public UpdateFilter(dispatch: (action: Actions.IResourceDashboardFilterUpdated) => any, filter: any) {
        dispatch(
            {
                filter,
                type: ActionTypes.RESOURCE_DASHBOARD_FILTER_UPDATED                
            });
    }

    public ApplyFilter(filter: any) {
        return ((dispatch: any, getState: any) => {
            const state = getState() as IApplicationState;

            this.UpdateFilter(dispatch, filter);
            this.RequestEvents(dispatch, state.common.activeProjects, filter);
        });
    }
    private Milestones(activeProjects: Project[], userIds: number[]): Promise<Milestone[]> {
        const promises = activeProjects
            .filter(p => p.projectManager !== null && userIds.indexOf(p.projectManager.id) >= 0)
            .map(p => api.MilestonesByProject(p));

        return Promise.all(promises)
            .then(results => results.reduce((prev, curr) => [...prev, ...curr], []));
    }

    private Tasks(activeProjects: Project[], userIds: number[]): Promise<Task[]> {
        const promises = activeProjects
            .map(p => api.TasksAssignedToUsers(p.deltekProjectNumber, userIds));

        return Promise.all(promises)
            .then(results => results.reduce((prev, curr) => [...prev, ...curr], []));
    }

    private RequestEvents(dispatch: (action: Actions.RequestEventsAction) => any, activeProjects: Project[], filter: any) {
        const users: SPUser[] = filter.users;
        const includeTasks: boolean = filter.includeTasks;
        const includeMilestones: boolean = filter.includeMilestones;
        const userIds = users.map(u => u.id);

        const tasksPromise = includeTasks ?
            this.Tasks(activeProjects, userIds) :
            Promise.resolve([]);

        const milestonesPromise = includeMilestones ?
            this.Milestones(activeProjects, userIds) :
            Promise.resolve([]);

        Promise.all([tasksPromise, milestonesPromise])
            .then((results: [Task[], Milestone[]]) => {
                const tasks = results[0];
                const milestones = results[1];

                dispatch(
                    {
                        milestones,
                        tasks,
                        type: ActionTypes.RECEIVE_EVENTS
                    });
            })
            .catch(error => {
                Logger.error(error);
                dispatch({ type: ActionTypes.EVENTS_REQUEST_ERROR });
            });
        
        dispatch({ type: ActionTypes.REQUEST_EVENTS });
    }
}

const actionCreators = new ActionCreators();
export default actionCreators;


