import * as React from 'react';
import { IUsersDropdownProps } from './UsersDropdownProps';

export default class UsersDropdown extends React.PureComponent<IUsersDropdownProps> {
    constructor(props: IUsersDropdownProps) {
        super(props);
        this.onUserSelected = this.onUserSelected.bind(this);
        this.defaultValue = this.defaultValue.bind(this);
    }

    public onUserSelected(e: any) {
        this.props.onUserSelected(Number(e.target.value));
    }

    public defaultValue() {
        if (this.props.selectedUser != null) {
            return this.props.selectedUser.id.toString();
        } else {
            return '';
        }
    }
    
    public render() {
        return <div className="persons">
            <select className="form-control" defaultValue={this.defaultValue()} onChange={this.onUserSelected}>
                <option />
                {
                    this.props.users.map(user => 
                        <option key={user.id} value={user.id}>{user.displayName}</option>)
                }
            </select>
        </div>
    }
}


