import * as React from 'react';
import TaskDesktop from '../TaskDesktop/TaskDesktop';
import TaskMobile from '../TaskMobile/TaskMobile';
import TasksCollapsePanel from '../TasksCollapsePanel/TasksCollapsePanel';
import { ITasksByProcessProps } from './TasksByProcessProps';

export default class TasksByProcess extends React.PureComponent<ITasksByProcessProps> {
    public render() {
        return <div>
            <TasksCollapsePanel
                title={"Tasks - assigned to me"}
                onTaskEdit={this.props.onTaskEdit}
                tasks={this.props.assignedToTasks}
                renderTaskDesctop={TaskDesktop}
                defaultExpanded={true}
                renderTaskMobile={TaskMobile} />

            <TasksCollapsePanel
                title={"Tasks I've assigned"}
                onTaskEdit={this.props.onTaskEdit}
                tasks={this.props.assignedByTasks}
                renderTaskDesctop={TaskDesktop}
                defaultExpanded={false}
                renderTaskMobile={TaskMobile} />
        </div>
    }
}


