import * as moment from 'moment';

import { Web } from '@pnp/sp';

import Consts from '../Consts';
import SPLookupValue from '../models/SPLookupValue';

export default class Repo {
    protected RootWeb: Web;
    
    constructor() {
        this.RootWeb = new Web(Consts.RootWebUrl);
    }

    protected GetProjectWeb(deltekProjectNumber: string): Web {
        const url = this.ToProjectSiteUrl(deltekProjectNumber);
        return this.GetWeb(url);
    }

    protected GetWeb(url: string): Web {
        return new Web(url);
    }

    protected LookupTitle(fieldName: string): string {
        return `${fieldName}/Title`;
    }

    protected LookupId(fieldName: string): string {
        return `${fieldName}/ID`;
    }

    protected LookupIdAndTitle(fieldName: string): string {
        return `${this.LookupTitle(fieldName)},${this.LookupId(fieldName)}`;
    }

    protected ToUrl(urlFieldValue: any) {
        if (urlFieldValue !== undefined && urlFieldValue !== null) {
            return urlFieldValue.Url;
        }
        return '';
    }

    protected ToUtcFormat(utcDate: string) {
        const momentDate = moment(utcDate);
        if(momentDate.isValid()) {
            return momentDate.format('YYYY-MM-DD[T]HH:mm:ss[Z]');
        }
        return '';
    }

    protected ToLookup(lookupFieldValue: any): SPLookupValue | null {
        if (lookupFieldValue !== undefined && lookupFieldValue !== null) {
            const lookupValue = new SPLookupValue();
            lookupValue.displayName = lookupFieldValue.Title;
            lookupValue.id = lookupFieldValue.ID;
            return lookupValue;
        }
        return null;
    }

    protected ToLookupMulti(lookupFieldValues: any[]): SPLookupValue[] {
        if (lookupFieldValues !== undefined && lookupFieldValues !== null) {
            return lookupFieldValues
                .map((lookupFieldValue: any) => this.ToLookup(lookupFieldValue)) as SPLookupValue[];
        }

        return [];
    }

    protected ToProjectSiteUrl(deltekProjectNumber: string): string {
        return `${Consts.RootWebUrl}/${deltekProjectNumber}`;
    }
}


