import * as React from 'react';
import { FormControl } from 'react-bootstrap';
import { isUri } from 'valid-url';
import { IUrlLinkProps } from './UrlLinkProps';
import { IUrlLinkState } from './UrlLinkState';

export default class UrlLink extends React.PureComponent<IUrlLinkProps, IUrlLinkState> {
    constructor(props: IUrlLinkProps) {
        super(props);

        this.state = {
            currentValue: props.initialValue
        }

        this.onUpdated = this.onUpdated.bind(this);
    }

    public onUpdated(e: any) {
        const newValue = e.target.value;
        let isValid = true;

        if (newValue && newValue !== '' && !isUri(newValue)) {
            isValid = false;
        }

        this.setState({ currentValue: newValue });
        this.props.onUpdated(newValue, isValid);
    }

    public render() {
        return <FormControl
                    onChange={this.onUpdated}
                    type="text"
                    value={this.state.currentValue}
                    placeholder="Please provide" />;
    }
}


