import * as React from 'react';
import { Button, ButtonToolbar, Checkbox } from 'react-bootstrap';

import SPUSer from '../../models/SPUser';
import StringMultipleSelect from '../StringMultipleSelect/StringMultipleSelect';
import UsersMultipleSelect from '../Users/UsersMultipleSelect/UsersMultipleSelect';
import { IResourceFilterProps } from './ResourceFilterProps';
import { IResourceFilterState } from './ResourceFilterState';

export default class ResourceFilter extends React.PureComponent<IResourceFilterProps, IResourceFilterState> {
    constructor(props: IResourceFilterProps) {
        super(props);

        this.resetFilter = this.resetFilter.bind(this);
        this.applyFilter = this.applyFilter.bind(this);
        this.usersByOffices = this.usersByOffices.bind(this);
        this.onUsersSelectionChanged = this.onUsersSelectionChanged.bind(this);
        this.onOfficesSelectionChanged = this.onOfficesSelectionChanged.bind(this);
        this.onTasksCheckboxChanged = this.onTasksCheckboxChanged.bind(this);
        this.onMilestonesCheckboxChanged = this.onMilestonesCheckboxChanged.bind(this);
        this.canApply = this.canApply.bind(this);

        this.state = {
            milestonesFilter: props.milestonesFilter,
            selectedOffices: props.selectedOffices,
            selectedUsers: props.selectedUsers,
            tasksFilter: props.tasksFilter
        }
    }

    public resetFilter() {
        this.setState({
            milestonesFilter: false,
            selectedOffices: [],
            selectedUsers: [],
            tasksFilter: false
        });
    }

    public applyFilter() {
        const filterData = {
            includeMilestones: this.state.milestonesFilter,
            includeTasks: this.state.tasksFilter,
            offices: this.state.selectedOffices.slice(),
            users: this.state.selectedUsers.slice()
        }

        this.props.onApplyFilter(filterData);
    }

    public usersByOffices() {
        if (this.state.selectedOffices.length === 0) { return []; }

        return this.props.allUsers
            .filter(u => this.state.selectedOffices.indexOf(u.office) >= 0);
    }

    public onUsersSelectionChanged(users: SPUSer[]) {
        this.setState({
            ...this.state,
            selectedUsers: users
        });
    }

    public onOfficesSelectionChanged(offices: string[]) {
        const selectedUsers = this.state.selectedUsers
            .filter(u => offices.indexOf(u.office) >= 0);

        this.setState({
            ...this.state,
            selectedOffices: offices,
            selectedUsers
        });
    }

    public onTasksCheckboxChanged() {
        this.setState({
            ...this.state,
            tasksFilter: !this.state.tasksFilter
        });
    }

    public onMilestonesCheckboxChanged() {
        this.setState({
            ...this.state,
            milestonesFilter: !this.state.milestonesFilter
        });
    }

    public canApply() {
        const eventsTypeSelected = this.state.tasksFilter || this.state.milestonesFilter;
        const usersSelected = this.state.selectedUsers.length > 0;

        return eventsTypeSelected && usersSelected;
    }

    public render() {
        return <div className='row'>
            <div className="col-sm-2">
                <StringMultipleSelect
                    items={this.props.allOffices}
                    title="Select offices"
                    selectedItemsAreaVisible={true}
                    selectedItems={this.state.selectedOffices}
                    onSelectionChanged={this.onOfficesSelectionChanged} />
            </div>
            <div className="col-sm-8">
                <UsersMultipleSelect
                    users={this.usersByOffices()}
                    selectedUsers={this.state.selectedUsers}
                    onSelectionChanged={this.onUsersSelectionChanged} />
            </div>
            <div className="col-sm-2">
                <br />
                <Checkbox inline={true} checked={this.state.tasksFilter} onChange={this.onTasksCheckboxChanged}>Tasks</Checkbox>
                <Checkbox inline={true} checked={this.state.milestonesFilter} onChange={this.onMilestonesCheckboxChanged}>Milestones</Checkbox>
                <br />
                <br />
                <ButtonToolbar>
                    <Button bsSize="small" bsStyle="primary" onClick={this.applyFilter} disabled={!this.canApply()}>Apply Filter</Button>
                    <Button bsSize="small" bsStyle="primary" onClick={this.resetFilter}>Reset Filter</Button>
                </ButtonToolbar>
            </div>
        </div>
    }
}


