import { connect } from 'react-redux';

import actionCrearors from '../../actions/ResourceDashboard/ActionCreators';
import ResourceFilter from '../../components/ResourceFilter/ResourceFilter';
import { IApplicationState } from '../../store';

const mapStateToProps = (state: IApplicationState) => {
    return{
        allOffices: state.common.allOffices,
        allUsers: state.common.allUsers,
        milestonesFilter: state.resourceDashboard.filter.includeMilestones,
        selectedOffices: state.resourceDashboard.filter.offices,
        selectedUsers: state.resourceDashboard.filter.users,
        tasksFilter: state.resourceDashboard.filter.includeTasks
    }
}

export default connect(mapStateToProps,
    {
        onApplyFilter: actionCrearors.ApplyFilter.bind(actionCrearors)
    })(ResourceFilter);


