import { Logger } from '@pnp/logging';

import api from '../../api/api';
import Project from '../../models/Project';
import Role from '../../models/Role';
import SPUser from '../../models/SPUser';
import Task from '../../models/Task';
import { IApplicationState } from '../../store';
import ActionTypes from '../ActionTypes';
import * as Actions from './IActions';

class ActionCreators {

    public UserSelected(userId: number) {
        return ((dispatch: any, getState: any) => {
            const state = getState() as IApplicationState;
            const selectedUser = state.common.allUsers
                .find(u => u.id === userId);

            if (selectedUser !== undefined) {
                this.ResetUserData(dispatch);
                this.SelectUser(dispatch, selectedUser);
                this.RequestProjects(dispatch, selectedUser.id, state.common.activeProjects);
            }
        });
    }

    public ProjectSelected(project: Project) {
        return ((dispatch: any, getState: any) => {

            const state = getState() as IApplicationState;
            const user = state.projectsDashboard.user;

            this.ResetUserData(dispatch);
            this.SelectProject(dispatch, project);

            if (user !== null) {
                this.RequsetProjectData(dispatch, project.deltekProjectNumber, user.id);
            }
        });
    }
    private RequestProjects(dispatch: (action: Actions.ProjectsByUserAction) => any, userId: number, activeProjects: Project[]) {
        const tasksPromises: Array<Promise<Task[]>> = activeProjects
            .map(p => api.TasksRelatedToUser(p.deltekProjectNumber, userId, 1));

        Promise.all(tasksPromises).then(results => {
            const tasksRelatedToUser = results
                .reduce((tasks, newTasks) => [...tasks, ...newTasks], []);

            const activeProjectsNumbers = new Set<string>();
            tasksRelatedToUser.forEach(t => {
                activeProjectsNumbers.add(t.deltekProjectNumber)
            });

            dispatch({
                projects: activeProjects
                    .filter(p => activeProjectsNumbers.has(p.deltekProjectNumber)),
                type: ActionTypes.RECEIVE_PROJECTS_BY_USER
            });
        })
        .catch(error => {
            Logger.error(error);
            dispatch({
                type: ActionTypes.PROJECTS_BY_USER_REQUEST_ERROR
            });
        });

        dispatch({
            type: ActionTypes.REQUEST_PROJECTS_BY_USER
        });
    }

    private RequsetProjectData(dispatch: (action: Actions.ProjectDataAction) => any, deltekProjectNumber: string, userId: number) {
        const tasksPromise = api.TasksRelatedToUser(deltekProjectNumber, userId);
        const rolesPromise = api.Roles(deltekProjectNumber);

        Promise.all([tasksPromise, rolesPromise])
            .then((results: [Task[], Role[]]) => {
                const tasks = results[0];
                const roles = results[1];

                dispatch(
                    {
                        roles,
                        tasks,
                        type: ActionTypes.RECEIVE_PROJECT_DATA                        
                    });
            })
            .catch(error => {
                Logger.error(error);
                dispatch({
                    type: ActionTypes.PROJECT_DATA_REQUEST_ERROR
                });
            });
        
        dispatch({
            type: ActionTypes.REQUEST_PROJECT_DATA
        });
    }

    private ResetUserData(dispatch: (action: Actions.IProjectsDashboardResetUserDataAction) => any) {
        dispatch({
            type: ActionTypes.PROJECTS_DASHBOARD_RESET_USER_DATA
        });
    }

    private SelectUser(dispatch: any, user: SPUser) {
        dispatch({
            type: ActionTypes.PROJECTS_DASHBOARD_USER_SELECTED,
            user
        });
    }

    private SelectProject(dispatch: any, project: Project) {
        dispatch({
            project,
            type: ActionTypes.PROJECTS_DASHBOARD_PROJECT_SELECTED            
        });
    }
};

const actionCreators = new ActionCreators();
export default actionCreators;


