import { Action, Reducer } from 'redux';

import ActionTypes from '../../actions/ActionTypes';
import {
    AddSingleTaskAction, RolesByProjectAction
} from '../../actions/AddSingleTaskForm/IActions';
import AddSingleTaskFormState from '../../store/Modals/AddSingleTaskFormState';

const initialState = {
    addSingleTaskRequest: false,
    addSingleTaskRequestError: false,

    rolesByProject: [],
    rolesByProjectRequest: false,
    rolesByProjectRequestError: false
}

export const AddSingleTaskFormReducer: Reducer<AddSingleTaskFormState> = (state: AddSingleTaskFormState = initialState, incomingAction: Action) => {
    const action = incomingAction as RolesByProjectAction | AddSingleTaskAction;

    switch (action.type) {
        case ActionTypes.REQUEST_ROLES_BY_PROJECT:
            {
                return {
                    ...state,
                    rolesByProjectRequest: true
                };
            }
        case ActionTypes.ROLES_BY_PROJECT_REQUEST_ERROR:
            {
                return {
                    ...state,
                    rolesByProjectRequest: false,
                    rolesByProjectRequestError: true
                };
            }
        case ActionTypes.RECEIVE_ROLES_BY_PROJECT:
            {
                return {
                    ...state,
                    rolesByProject: action.roles,
                    rolesByProjectRequest: false,
                    rolesByProjectRequestError: false                    
                };
            }
        case ActionTypes.RESET_ROLES_BY_PROJECT:
            {
                return {
                    ...state,
                    rolesByProject: [],
                    rolesByProjectRequest: false,
                    rolesByProjectRequestError: false                    
                };
            }
        case ActionTypes.ADD_SINGLE_TASK_REQUEST:
            {
                return {
                    ...state,
                    addSingleTaskRequest: true,
                    addSingleTaskRequestError: false
                };
            }
        case ActionTypes.ADD_SINGLE_TASK_REQUEST_ERROR:
            {
                return {
                    ...state,
                    addSingleTaskRequest: false,
                    addSingleTaskRequestError: true
                };
            }
        case ActionTypes.ADD_SINGLE_TASK_REQUEST_SUCCESS:
            {
                return {
                    ...state,
                    addSingleTaskRequest: false,
                    addSingleTaskRequestError: false
                };
            }
        default: {
            return state;
        }
    }
};


