import Consts from '../Consts';
import TaskTemplate from '../models/TaskTemplate';
import Repo from './Repo';

export default class TaskTemplatesRepo extends Repo {
    private consts = Consts.TaskTemplatesListConsts;

    public GetAll(projectTemplateSiteUrl: string): Promise<TaskTemplate[]> {
        const fields = [
            this.consts.Fields.DeliverableFieldName,
            this.consts.Fields.MilestoneFieldName,
            this.consts.Fields.TeamMemberFieldName,
            this.consts.Fields.TaskNameFieldName,
            'Id'
        ].join(',');

        const web = this.GetWeb(projectTemplateSiteUrl);

        return web.lists.getByTitle(this.consts.ListTitle).items
            .select(fields)
            .getAll()
            .then(items => items.map(i => this.ToTaskTemplate(i)));
    }

    private ToTaskTemplate(item: any): TaskTemplate {
        const taskTemplate: TaskTemplate = new TaskTemplate();

        taskTemplate.id = item.Id;
        taskTemplate.deliverable = item[this.consts.Fields.DeliverableFieldName];
        taskTemplate.milestone = item[this.consts.Fields.MilestoneFieldName];
        taskTemplate.teamMember = item[this.consts.Fields.TeamMemberFieldName];
        taskTemplate.title = item[this.consts.Fields.TaskNameFieldName];

        return taskTemplate;
    }
}


