import { Action, Reducer } from 'redux';

import ActionTypes from '../../actions/ActionTypes';
import { AddModulesAction, ProjectTemplateDataAction } from '../../actions/AddModulesForm/IActions';
import AddModulesFormState from '../../store/Modals/AddModulesFormState';

const initialState = {
    projectTemplateDataRequest: false,
    projectTemplateDataRequestError: false,
    projectTemplateTasks: [],

    addModulesRequest: false,
    addModulesRequestError: false
}

export const AddModulesFormReducer: Reducer<AddModulesFormState> = (state: AddModulesFormState = initialState, incomingAction: Action) => {
    const action = incomingAction as ProjectTemplateDataAction
        | AddModulesAction;

    switch (action.type) {
        case ActionTypes.REQUEST_PROJECT_TEMPLATE_DATA:
            {
                return {
                    ...state,
                    projectTemplateDataRequest: true
                };
            }
        case ActionTypes.PROJECT_TEMPLATE_DATA_REQUEST_ERROR:
            {
                return {
                    ...state,
                    projectTemplateDataRequest: false,
                    projectTemplateDataRequestError: true
                };
            }
        case ActionTypes.RECEIVE_PROJECT_TEMPLATE_DATA:
            {
                return {
                    ...state,
                    projectTemplateDataRequest: false,
                    projectTemplateDataRequestError: false,
                    projectTemplateTasks: action.taskTemplates
                };
            }
        case ActionTypes.RESET_PROJECT_TEMPLATE_DATA:
            {
                return {
                    ...state,
                    projectTemplateDataRequest: false,
                    projectTemplateDataRequestError: false,
                    projectTemplateTasks: []
                };
            }
        case ActionTypes.ADD_MODUES_REQUEST:
            {
                return {
                    ...state,
                    addModulesRequest: true
                };
            }
        case ActionTypes.ADD_MODUES_REQUEST_ERROR:
            {
                return {
                    ...state,
                    addModulesRequest: false,
                    addModulesRequestError: true
                };
            }
        case ActionTypes.ADD_MODUES_REQUEST_SUCCESS:
            {
                return {
                    ...state,
                    addModulesRequest: false,
                    addModulesRequestError: false
                };
            }
        default: {
            return state;
        }
    }
};


