import * as React from 'react';

import MultipleSelect from '../MultipleSelect/MultipleSelect';
import { IStringMultipleSelectProps } from './StringMultipleSelectProps';

export default class StringMultipleSelect extends React.PureComponent<IStringMultipleSelectProps> {
    constructor(props: IStringMultipleSelectProps) {
        super(props);

        this.titleExtractor = this.titleExtractor.bind(this);
        this.valueExtractor = this.valueExtractor.bind(this);
    }

    public titleExtractor(milestone: string) {
        return milestone;
    }

    public valueExtractor(milestone: string) {
        return milestone;
    }

    public render() {
        type MultipleSelectType = new () => MultipleSelect<string>;
        const MultipleSelectString = MultipleSelect as MultipleSelectType;

        return <MultipleSelectString
            items={this.props.items}
            selectAllButtonVisible={this.props.selectAllButtonVisible}
            selectedItemsAreaVisible={this.props.selectedItemsAreaVisible}
            id="string-multiple-select"
            onSelectionChanged={this.props.onSelectionChanged}
            selectedItems={this.props.selectedItems}
            title={this.props.title}
            titleExtractor={this.titleExtractor}
            valueExtractor={this.valueExtractor} />
    }
}


