import { connect } from 'react-redux';

import actionCreators from '../../actions/AddModulesForm/ActionCreators';
import AddModulesForm from '../../components/AddModulesForm/AddModulesForm';
import { IApplicationState } from '../../store';

const mapStateToProps = (state: IApplicationState, ownProps: any) => {
    const modules = state.addModulesFormState
        .projectTemplateTasks
        .map(t => t.milestone);

    return {
        modules: Array.from(new Set(modules)),
        projectTemplates: state.common.projectTemplates,
        projects: state.common.activeProjects,
        selectedProject: ownProps.project
    }
}

const mapDispatchToProps = {
    onAddModules: actionCreators.AddModules.bind(actionCreators),
    onProjectTemplateSelected: actionCreators.ProjectTemplateSelected.bind(actionCreators)
}

export default connect(
    mapStateToProps, mapDispatchToProps)(AddModulesForm);


