import Consts from '../Consts';
import Project from '../models/Project';
import Repo from './Repo';

export default class ProjectsRepo extends Repo {
    private consts = Consts.ProjectsListConsts;

    private fields = [
        this.consts.Fields.ProfitCenterFieldName,
        this.consts.Fields.ProjectNamelFieldName,
        this.consts.Fields.DeltekProjectNumberFieldName,
        this.consts.Fields.SiteUrlFieldName,
        this.LookupIdAndTitle(this.consts.Fields.PhasesFieldName),
        this.LookupIdAndTitle(this.consts.Fields.PrincipalFieldName),
        this.LookupIdAndTitle(this.consts.Fields.ProjectManagerFieldName),
        'Id'
    ];

    private expand = [
        this.consts.Fields.PhasesFieldName,
        this.consts.Fields.PrincipalFieldName,
        this.consts.Fields.ProjectManagerFieldName
    ];

    public GetAll(): Promise<Project[]> {
        const filter = `(${this.consts.Fields.SiteCreatedFieldName} eq 1)`;
        return this.RootWeb.lists.getByTitle(this.consts.ListTitle).items
            .select(this.fields.join(','))
            .expand(this.expand.join(','))
            .filter(filter)
            .get()
            .then(items => items.map(i => this.ToProject(i)));
    }

    private ToProject(item: any): Project {
        const project: Project = new Project();
        project.id = item.Id;

        project.principal = this.ToLookup(item[this.consts.Fields.PrincipalFieldName]);
        project.projectManager = this.ToLookup(item[this.consts.Fields.ProjectManagerFieldName]);
        project.phases = this.ToLookupMulti(item[this.consts.Fields.PhasesFieldName]);

        project.deltekProjectNumber = item[this.consts.Fields.DeltekProjectNumberFieldName];
        project.profitCenter = item[this.consts.Fields.ProfitCenterFieldName];
        project.projectName = item[this.consts.Fields.ProjectNamelFieldName];

        project.siteUrl = this.ToUrl(item[this.consts.Fields.SiteUrlFieldName]);

        return project;
    }
}


