import Task from '../models/Task';
import ProjectsDashboardState from '../store/Dashboards/ProjectsDashboardState';
import TeamDashboardState from '../store/Dashboards/TeamDashboardState';
import ActionTypes from './ActionTypes';

export default class AddTasksActionsCreatorBase {

    protected TryUpdateProjectsDashboardTasks(dispatch: any, state: ProjectsDashboardState, ...addedTasks: Task[]) {
        const newTasksForProjectsDashboard = this
            .NewTasksForProjectsDashboard(state, addedTasks);

        if (newTasksForProjectsDashboard.length >= 0) {
            dispatch({
                tasks: [...state.tasks, ...newTasksForProjectsDashboard],
                type: ActionTypes.PROJECTS_DASHBOARD_TASKS_UPDATED
            });
        }
    }

    protected TryUpdateTeamDashboardTasks(dispatch: any, state: TeamDashboardState, ...addedTasks: Task[]) {
        const newTasksForTeamDashboard = this
            .NewTasksForTeamDashboard(state, addedTasks);

        if (newTasksForTeamDashboard.length > 0) {

            dispatch({
                tasks: [...state.tasks, ...newTasksForTeamDashboard],
                type: ActionTypes.TEAM_DASHBOARD_TASKS_UPDATED               
            });
        }
    }
    private NewTasksForProjectsDashboard(state: ProjectsDashboardState, addedTasks: Task[]): Task[] {
        const selectedProject = state.project;
        const selectedUser = state.user;

        if (selectedProject !== null && selectedUser !== null) {
            return addedTasks
                .filter(t => t.deltekProjectNumber === selectedProject.deltekProjectNumber)
                .filter(t => t.assignedTo.some(user => user.id === selectedUser.id) ||
                    t.assignedBy !== null && t.assignedBy.id === selectedUser.id);
        }

        return [];
    }

    private NewTasksForTeamDashboard(state: TeamDashboardState, addedTasks: Task[]): Task[] {
        const selectedUser = state.user;

        if (selectedUser !== null) {
            return addedTasks
                .filter(t => t.assignedTo
                    .some(u => u.id === selectedUser.id));
        }

        return [];
    }
}


