import * as React from 'react';
import { Col, Row } from 'react-bootstrap';

import ActiveUsersListContainer from '../containers/TeamDashboard/ActiveUsersListContainer';
import OfficesMultipleSelectContainer from '../containers/TeamDashboard/OfficesMultipleSelectContainer';
import TasksByUserContainer from '../containers/TeamDashboard/TasksByUserContainer';

export default class TeamDashboard extends React.Component<any, any> {
    public render() {
        return <Row>
            <Col sm={3}>
                <OfficesMultipleSelectContainer />
                <br />
                <ActiveUsersListContainer />
            </Col>
            <Col sm={9}>
                <TasksByUserContainer />
            </Col>
        </Row>
    }
}


