import { Logger } from '@pnp/logging';

import api from '../../api/api';
import Project from '../../models/Project';
import SPUser from '../../models/SPUser';
import Task from '../../models/Task';
import { IApplicationState } from '../../store';
import ActionTypes from '../ActionTypes';
import * as Actions from './IActions';

class ActionCreators {
    public RequestActiveUsers(dispatch: (action: Actions.ActiveUsersAction) => any, activeProjects: Project[]) {
        const tasksPromises: Array<Promise<Task[]>> = activeProjects
            .map(p => api.ActiveTasksByProject(p.deltekProjectNumber));
        Promise.all(tasksPromises).then(results => {
            const activeTasks = results
                .reduce((tasks, newTasks) => [...tasks, ...newTasks], []);

            const activeUsersIds = activeTasks
                .map(t => t.assignedTo)
                .reduce((users, newUsers) => [...users, ...newUsers])
                .map(u => u.id);

            dispatch({
                type: ActionTypes.RECEIVE_ACTIVE_USERS,
                usersIds: Array.from(new Set(activeUsersIds))
            });
        })
        .catch(error => {
            Logger.error(error);
            dispatch({
                type: ActionTypes.ACTIVE_USERS_REQUEST_ERROR
            });
        });

        dispatch({
            type: ActionTypes.REQUEST_ACTIVE_USERS
        });
    }

    public RequestTasksByUser(dispatch: (action: Actions.TasksByUserAction) => any, activeProjects: Project[], userId: number) {
        const tasksPromises: Array<Promise<Task[]>> = activeProjects
            .map(p => api.ActiveTasksByProjectAndUser(p.deltekProjectNumber, userId));

        Promise.all(tasksPromises).then(results => {
            const activeTasks = results
                .reduce((tasks, newTasks) => [...tasks, ...newTasks], []);

            dispatch({
                tasks: activeTasks,
                type: ActionTypes.RECEIVE_TASKS_BY_USER               
            });
        })
        .catch(error => {
            Logger.error(error);
            dispatch({
                type: ActionTypes.TASKS_BY_USER_REQUEST_ERROR
            });
        });

        dispatch({
            type: ActionTypes.REQUEST_TASKS_BY_USER
        });
    }

    public OfficesUpdated(offices: string[]) {
        return ((dispatch: any, getState: any) => {
            this.UpdateOffices(dispatch, offices);
            const state = getState() as IApplicationState;

            if (!state.teamDashboard.activeUsersRequested) {
                this.RequestActiveUsers(dispatch, state.common.activeProjects);
            }
        });
    }

    public UserSelected(user: SPUser) {
        return ((dispatch: any, getState: any) => {
            this.UpdateSelectedUser(dispatch, user);
            const state = getState() as IApplicationState;

            this.RequestTasksByUser(dispatch, state.common.activeProjects, user.id);
        });
    }

    private UpdateSelectedUser(dispatch: (action: Actions.ITeamDashboardUserSelectedAction) => any, user: SPUser) {
        dispatch(
            {
                type: ActionTypes.TEAM_DASHBOARD_USER_SELECTED,
                user
            });
    }

    private UpdateOffices(dispatch: (action: Actions.ITeamDashboardSelectedOfficesUpdatedAction) => any, offices: string[]) {
            dispatch(
            {
                offices,
                type: ActionTypes.TEAM_DASHBOARD_OFFICES_UPDATED               
            });
    }
};

const actionCreators = new ActionCreators();
export default actionCreators;


