import { connect } from 'react-redux';
import actionCreators from '../../actions/TeamDashboard/ActionCreators';
import UsersSearchableList from '../../components/Users/UsersSearchableList/UsersSearchableList';
import SPUser from '../../models/SPUser';
import { IApplicationState } from '../../store';

const mapStateToProps = (state: IApplicationState) => {
    const spUsersMap = new Map(
        state.common.allUsers.map(u => [u.id, u] as [number, SPUser])
    );

    const spUsers = state.teamDashboard.activeUsers
        .filter(userId => spUsersMap.has(userId))
        .map(userId => spUsersMap.get(userId));

    const filteredSPUsers = spUsers
        .filter(u => u !== undefined && state.teamDashboard.offices.indexOf(u.office) >= 0);

    return {
        selectedUser: state.teamDashboard.user,
        users: filteredSPUsers
    }
}

export default connect(
    mapStateToProps, {
        onUserSelected: actionCreators.UserSelected.bind(actionCreators)
    })(UsersSearchableList);


