import { CommonReducer } from '../reducers/Common/CommonReducer';
import { RootModalDialogReducer } from '../reducers/Common/RootModalDialogReducer';
import { ProjectsDashboardReducer } from '../reducers/Dashboards/ProjectsDashboardReducer';
import { ResourceDashboardReducer } from '../reducers/Dashboards/ResourceDashboardReducer';
import { TeamDashboardReducer } from '../reducers/Dashboards/TeamDashboardReducer';
import { AddModulesFormReducer } from '../reducers/ModalDialogs/AddModulesFormReducer';
import { AddSingleTaskFormReducer } from '../reducers/ModalDialogs/AddSingleTaskFormReducer';
import { UpdateTaskFormReducer } from '../reducers/ModalDialogs/UpdateTaskFormReducer';
import ICommonState from './Common/CommonState';
import IRootModalDialogState from './Common/RootModalDialogState';
import IProjectsDashboardState from './Dashboards/ProjectsDashboardState';
import IResourceDashboardState from './Dashboards/ResourceDashboardState';
import ITeamDashboardState from './Dashboards/TeamDashboardState';
import IAddModulesFormState from './Modals/AddModulesFormState';
import IAddSingleTaskFormState from './Modals/AddSingleTaskFormState';
import IUpdateTaskFormState from './Modals/UpdateTaskFormState';

export interface IApplicationState {
    // common
    common: ICommonState,

    // modals
    addModulesFormState: IAddModulesFormState,
    rootModalDialogState: IRootModalDialogState,
    addSingleTaskFormState: IAddSingleTaskFormState,
    updateTaskFormState: IUpdateTaskFormState,

    // dashboards
    projectsDashboard: IProjectsDashboardState,
    teamDashboard: ITeamDashboardState,
    resourceDashboard: IResourceDashboardState
}

export const reducers = {
    addModulesFormState: AddModulesFormReducer,
    addSingleTaskFormState: AddSingleTaskFormReducer,
    common: CommonReducer,
    projectsDashboard: ProjectsDashboardReducer,
    resourceDashboard: ResourceDashboardReducer,
    rootModalDialogState: RootModalDialogReducer,
    teamDashboard: TeamDashboardReducer,
    updateTaskFormState: UpdateTaskFormReducer
};

export type AppThunkAction<TAction> = 
(dispatch: (action: TAction) => void, getState: () => IApplicationState) => void;



