import * as React from "react";
import { ISPUserInfoProps } from "./SPUserInfoProps";

export default class SPUserInfo extends React.PureComponent<ISPUserInfoProps> {
    public render() {
        return <div>
            <h5>
                {this.props.item.displayName}
            </h5>
            <h6>
                <strong>{this.props.item.jobTitle} | {this.props.item.office}</strong>
            </h6>
        </div>;
    }
}


