import Consts from '../Consts';
import Task from '../models/Task';
import Repo from './Repo';

export default class TasksRepo extends Repo {
    private consts = Consts.TasksListConsts;

    private fields = [
        this.LookupIdAndTitle(this.consts.Fields.AssignedToFieldName),
        this.LookupIdAndTitle(this.consts.Fields.AssignedByFieldName),
        this.consts.Fields.NameFieldName,
        this.consts.Fields.StatusFieldName,
        this.consts.Fields.DueFieldName,
        this.consts.Fields.TemplateLinkFieldName,
        this.consts.Fields.DeliverableLinkFieldName,
        this.consts.Fields.VideoLinkFieldName,
        this.consts.Fields.DeliverableFieldName,
        this.consts.Fields.TeamMemberFieldName,
        this.consts.Fields.MilestoneFieldName,
        this.consts.Fields.PhaseFieldName,
        'Id'
    ]

    private expand = [
        this.consts.Fields.AssignedToFieldName,
        this.consts.Fields.AssignedByFieldName
    ]

    public GetById(deltekProjectNumber: string, taskId: number): Promise<Task> {
        const web = this.GetProjectWeb(deltekProjectNumber);
        return web.lists.getByTitle(this.consts.ListTitle)
                .items
                .getById(taskId)
                .select(this.fields.join(','))
                .expand(this.expand.join(','))
                .get()
                .then(item => this.ToTask(item, deltekProjectNumber));
    }

    public GetTasksRelatedToUser(deltekProjectNumber: string, userId: number, rowLimit: number = 0): Promise<Task[]> {
        const assignedToFilter = `${this.LookupId(this.consts.Fields.AssignedToFieldName)} eq ${userId}`;
        const assignedByFilter = `${this.LookupId(this.consts.Fields.AssignedByFieldName)} eq ${userId}`;
        const filter = `(${assignedToFilter}) or (${assignedByFilter})`;

        return this.GetTasksByFilter(deltekProjectNumber, filter, rowLimit);
    }

    public GetActiveTasksByProject(deltekProjectNumber: string): Promise<Task[]> {
        const completedStatusFilter = `${this.consts.Fields.StatusFieldName} ne '${Consts.TaskStatuces.Completed}'`;
        const reviewedStatusFilter = `${this.consts.Fields.StatusFieldName} ne '${Consts.TaskStatuces.Reviewed}'`;

        const filter = `(${completedStatusFilter}) and (${reviewedStatusFilter})`;
        return this.GetTasksByFilter(deltekProjectNumber, filter);
    }

    public GetActiveTasksByProjectAndUser(deltekProjectNumber: string, userId: number): Promise<Task[]> {
        const completedStatusFilter = `${this.consts.Fields.StatusFieldName} ne '${Consts.TaskStatuces.Completed}'`;
        const reviewedStatusFilter = `${this.consts.Fields.StatusFieldName} ne '${Consts.TaskStatuces.Reviewed}'`;
        const userFilter = `${this.consts.Fields.AssignedToFieldName} eq '${userId}'`;

        const filter = `(${completedStatusFilter}) and (${reviewedStatusFilter}) and (${userFilter})`;
        return this.GetTasksByFilter(deltekProjectNumber, filter);
    }

    public GetTasksAssignedToUsers(deltekProjectNumber: string, userIds: number[]): Promise<Task[]> {
        const chunks = this.ChunkArray(userIds.slice(), 7);
        const promises: Array<Promise<Task[]>> = [];
        chunks.forEach(chunk => {
            const restrictions = chunk
                .map(id => `(${this.LookupId(this.consts.Fields.AssignedToFieldName)} eq ${id})`);
            const chunkFilter = restrictions.join('or');
            const chunkPromise = this.GetTasksByFilter(deltekProjectNumber, chunkFilter);

            promises.push(chunkPromise);
        });

        return Promise.all(promises)
            .then(results => results
            .reduce((acc, val) => [...acc, ...val], []))
    }

    public Add(deltekProjectNumber: string, tasks: Task[]): Promise<Task[]> {
        return new Promise((resolve, reject) => {
            const web = this.GetProjectWeb(deltekProjectNumber);
            const list = web.lists.getByTitle(this.consts.ListTitle);

            list.getListItemEntityTypeFullName().then(entityTypeFullName => {

                const addItemsBatch = web.createBatch();
                const itemsToAdd = tasks
                    .map(t => this.ToListItem(t));

                const addedItemsIds: number[] = [];

                itemsToAdd.forEach(item => {
                    list.items.inBatch(addItemsBatch)
                        .add(item, entityTypeFullName)
                        .then(r => addedItemsIds.push(r.data.Id))
                });

                addItemsBatch.execute().then(() => {
                    const addedTasksPromises = addedItemsIds
                        .map(id => this.GetById(deltekProjectNumber, id))

                    Promise.all(addedTasksPromises)
                        .then(addedTasks => resolve(addedTasks));
                })
            })
        });
    }

    public Update(task: Task): Promise<Task> {
        const deltekProjectNumber = task.deltekProjectNumber;
        const web = this.GetProjectWeb(deltekProjectNumber);
        const listItemToUpdate = this.ToListItem(task);

        return web.lists.getByTitle(this.consts.ListTitle).items
                .getById(task.id)
                .update(listItemToUpdate)
                .then(r => this.GetById(deltekProjectNumber, task.id));
    }

    private ChunkArray(array: any[], chunkSize: number) {
        const results = [];
        while (array.length) {
            results.push(array.splice(0, chunkSize));
        }
        return results;
    }

    private ToTask(item: any, deltekProjectNumber: string): Task {
        const task: Task = new Task();
        task.id = item.Id;
        task.deltekProjectNumber = deltekProjectNumber;

        task.assignedBy = this.ToLookup(item[this.consts.Fields.AssignedByFieldName]);
        task.assignedTo = this.ToLookupMulti(item[this.consts.Fields.AssignedToFieldName]);

        task.deliverable = item[this.consts.Fields.DeliverableFieldName];
        task.due = item[this.consts.Fields.DueFieldName];
        task.milestone = item[this.consts.Fields.MilestoneFieldName];
        task.name = item[this.consts.Fields.NameFieldName];
        task.phase = item[this.consts.Fields.PhaseFieldName];
        task.status = item[this.consts.Fields.StatusFieldName];
        task.teamMember = item[this.consts.Fields.TeamMemberFieldName];

        task.deliverableLinkUrl = this.ToUrl(item[this.consts.Fields.DeliverableLinkFieldName]);
        task.templateLinkUrl = this.ToUrl(item[this.consts.Fields.TemplateLinkFieldName]);
        task.videoLinkUrl = this.ToUrl(item[this.consts.Fields.VideoLinkFieldName]);
        task.projectSiteUrl = this.ToProjectSiteUrl(deltekProjectNumber);

        return task;
    }

    private ToListItem(task: Task) {
        const item: any = {};

        item[`${this.consts.Fields.AssignedToFieldName}Id`] =
            {
                results: task.assignedTo
                    .map(e => e.id)
            };

        if (task.assignedBy) {
            item[`${this.consts.Fields.AssignedByFieldName}Id`] = task.assignedBy.id;
        }

        item[this.consts.Fields.DueFieldName] = this.ToUtcFormat(task.due);
        item[this.consts.Fields.NameFieldName] = task.name;
        item[this.consts.Fields.PhaseFieldName] = task.phase;
        item[this.consts.Fields.StatusFieldName] = task.status;
        item[this.consts.Fields.MilestoneFieldName] = task.milestone;
        item[this.consts.Fields.TeamMemberFieldName] = task.teamMember;
        item[this.consts.Fields.DeliverableFieldName] = task.deliverable;

        item[this.consts.Fields.VideoLinkFieldName] = { Url: task.videoLinkUrl};
        item[this.consts.Fields.TemplateLinkFieldName] = { Url: task.templateLinkUrl };
        item[this.consts.Fields.DeliverableLinkFieldName] = { Url: task.deliverableLinkUrl };
            
        return item;
    }

    private GetTasksByFilter(deltekProjectNumber: string, filter: string, rowLimit: number = 0): Promise<Task[]> {
        const web = this.GetProjectWeb(deltekProjectNumber);
        return web.lists.getByTitle(this.consts.ListTitle).items
            .select(this.fields.join(','))
            .expand(this.expand.join(','))
            .filter(filter)
            .top(rowLimit)
            .get()
            .then(items => items
            .map(item => this.ToTask(item, deltekProjectNumber)));
    }
}


