import { connect } from 'react-redux';

import actionCreators from '../../actions/TeamDashboard/ActionCreators';
import StringMultipleSelect from '../../components/StringMultipleSelect/StringMultipleSelect';
import { IApplicationState } from '../../store';

const mapStateToProps = (state: IApplicationState) => {
    return{
        items: state.common.allOffices,
        selectedItems: state.teamDashboard.offices,
        title: "Select offices"
    }
}

export default connect(
    mapStateToProps, {
         onSelectionChanged: actionCreators.OfficesUpdated.bind(actionCreators)
    })(StringMultipleSelect);


