import * as React from 'react';
import TaskDesktop from '../TaskDesktop/TaskDesktop';
import TaskMobile from '../TaskMobile/TaskMobile';
import TasksCollapsePanel from '../TasksCollapsePanel/TasksCollapsePanel';
import { ITasksByUserProps } from './TasksByUserProps';

export default class TasksByUser extends React.PureComponent<ITasksByUserProps> {
    public render() {
        return <div>
            <TasksCollapsePanel
                title={"My Overdue Tasks"}
                onTaskEdit={this.props.onTaskEdit}
                tasks={this.props.overdueTasks}
                renderTaskDesctop={TaskDesktop}
                renderTaskMobile={TaskMobile}
                defaultExpanded={true}
                bsStyle="danger" />

            <TasksCollapsePanel
                title={"My Remaining Tasks"}
                tasks={this.props.remainingTasks}
                onTaskEdit={this.props.onTaskEdit}
                renderTaskDesctop={TaskDesktop}
                renderTaskMobile={TaskMobile}
                defaultExpanded={false}
                bsStyle="info" />
        </div>
    }
}


