import * as React from 'react';
import { Badge, ListGroup, ListGroupItem, Panel } from 'react-bootstrap';

import { ISearchableListProps } from './SearchableListProps';
import { ISearchableListState } from './SearchableListState';

export default class SearchableList<T> extends React.PureComponent<ISearchableListProps<T>, ISearchableListState<T>> {
    constructor(props: ISearchableListProps<T>) {
        super(props);
        this.onSearchChange = this.onSearchChange.bind(this);
        this.filteredItems = this.filteredItems.bind(this);
        this.onSelect = this.onSelect.bind(this);

        this.state = { filteredItems: [] };
    }

    public componentDidMount() {
        this.setState({ filteredItems: this.props.items });
    }

    public componentWillReceiveProps(nextProps: ISearchableListProps<T>) {
        this.setState({ filteredItems: nextProps.items });
    }

    public isActive(item: T) {
        if (this.props.selectedItem) {
            return this.props.valueExtractor(this.props.selectedItem) === this.props.valueExtractor(item);
        }
        return false;
    }

    public render() {
        const RenderItem = this.props.renderItem;

        return <Panel className="items-panel" defaultExpanded={true}>
            <Panel.Heading>
                <Panel.Title toggle={true}>
                    {
                        this.props.title
                    }
                    <Badge pullRight={true}>{this.state.filteredItems.length}</Badge>
                </Panel.Title>
            </Panel.Heading>
            <Panel.Collapse>
                <Panel.Body>
                    <input type="text" onChange={this.onSearchChange} className="form-control" placeholder="Search" />
                    <br />
                    <ListGroup>
                        {
                            this.state.filteredItems.map(item =>
                                <ListGroupItem 
                                    onClick={this.onSelect}
                                    value={this.props.valueExtractor(item)}
                                    key={this.props.valueExtractor(item)} 
                                    active={this.isActive(item)}>
                                    {
                                        <RenderItem item={item} />
                                    }
                                </ListGroupItem>
                            )
                        }
                    </ListGroup>
                </Panel.Body>
            </Panel.Collapse>
        </Panel>;
    }

    private onSearchChange(e: any) {
        const filteredItems = this.filteredItems(e.target.value);
        this.setState({
            ...this.state,
            filteredItems
        });
    }

    private filteredItems(searchRequest: string) {
        let filteredItems = new Array<T>();
        if (searchRequest && searchRequest !== '') {
            filteredItems = this.props.items.filter(i => this.props.match(searchRequest, i));
        } else {
            filteredItems = this.props.items;
        }

        return filteredItems;
    }

    private onSelect(e: any) {
        const selectedValue = e.currentTarget.value;
        const item = this.props.items
            .find(i => this.props.valueExtractor(i) === selectedValue);
        if(item) {
            this.props.onItemSelected(item);
        }
    }
}


