import { connect } from 'react-redux';

import actionCreators from '../../actions/RootModal/ActionCreators';
import TasksByUser from '../../components/Tasks/TasksByUser/TasksByUser';
import Task from '../../models/Task';
import { IApplicationState } from '../../store';

const mapStateToProps = (state: IApplicationState) => {
    const overdueTasks = state.teamDashboard.tasks
        .filter(t => t.overdue);
    const remainingTasks = state.teamDashboard.tasks
        .filter(t => !t.overdue);
    return {
        overdueTasks,
        remainingTasks,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        onTaskEdit: (task: Task) => dispatch(
            actionCreators.OpenModalDialog('EDIT_TASK', { task }, 'Update Task')),
    }
}

export default connect(
    mapStateToProps, mapDispatchToProps)(TasksByUser);


