import * as React from 'react';
import { Button } from 'react-bootstrap';

import { ILogOutProps } from './LogOutProps';

const LogOut: React.SFC<ILogOutProps> = (props:ILogOutProps) => {
    return <Button
            onClick={props.onLogOut}
            bsStyle="primary"
            bsSize="small">Log out</Button>;
}
export default LogOut;



