import { connect } from 'react-redux';

import actionCreators from '../../actions/RootModal/ActionCreators';
import TasksByProcess from '../../components/Tasks/TasksByProcess.tsx/TasksByProcess';
import Task from '../../models/Task';
import { IApplicationState } from '../../store';

const mapStateToProps = (state: IApplicationState) => {
    
    const selectedUser = state.projectsDashboard.user;
    let assignedByTasks: Task[] = [];
    let assignedToTasks: Task[] = [];

    if (selectedUser !== null) {
        assignedByTasks = state.projectsDashboard.tasks
            .filter(t => t.assignedBy !== null && t.assignedBy.id === selectedUser.id);

        assignedToTasks = state.projectsDashboard.tasks
            .filter(t => t.assignedTo.some(u => u.id === selectedUser.id));
    }

    return {
        assignedByTasks,
        assignedToTasks,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        onTaskEdit: (task: Task) => dispatch(
            actionCreators.OpenModalDialog("EDIT_TASK", { task }, 'Update Task')),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(TasksByProcess);


