import * as React from 'react';
import { Col, Row } from 'react-bootstrap';

import ProjectCardContainer from '../containers/ProjectsDashboard/ProjectCardContainer';
import ProjectsListContainer from '../containers/ProjectsDashboard/ProjectsListContainer';
import TasksByProcessContainer from '../containers/ProjectsDashboard/TasksByProcessContainer';
import UsersDropdownContainer from '../containers/ProjectsDashboard/UsersDropdownContainer';

export default class ProjectsDashboard extends React.Component<any, any> {
    public render() {
        return <Row>
                <Col sm={3}>
                    <UsersDropdownContainer />
                    <br />
                    <ProjectsListContainer />
                </Col>
                <Col sm={9}>
                    <ProjectCardContainer />
                    <TasksByProcessContainer />
                </Col>
            </Row>
    }
}


