import { Logger } from '@pnp/logging';

import api from '../../api/api';
import Task from '../../models/Task';
import { AppThunkAction, IApplicationState } from '../../store';
import ActionTypes from '../ActionTypes';
import { IProjectsDashboardTasksUpdatedAction } from '../ProjectsDashboard/IActions';
import { ICloseDialogAction } from '../RootModal/IActions';
import { ITeamDashboardTasksUpdatedAction } from '../TeamDashboard/IActions';
import UpdateTaskAction from './IActions';

type DispatchAction = UpdateTaskAction
    | ICloseDialogAction
    | IProjectsDashboardTasksUpdatedAction
    | ITeamDashboardTasksUpdatedAction;

class ActionCreators {

    public UpdateTask(task: Task, formData: any): AppThunkAction<DispatchAction> {
        return ((dispatch, getState) => {

            const taskToUpdate = {
                ...task,
                deliverableLinkUrl: formData.deleverableLink,
                due: formData.due,
                status: formData.status
            }

            api.UpdateTask(taskToUpdate)
                .then(updatedTask => {
                    dispatch({
                        type: ActionTypes.TASK_UPDATE_REQUEST_SUCCESS
                    });
                    const state = getState() as IApplicationState;

                    this.CloseModalDialog(dispatch);

                    this.TryUpdateDashboardTasks(
                        dispatch,
                        state.projectsDashboard.tasks,
                        updatedTask,
                        ActionTypes.PROJECTS_DASHBOARD_TASKS_UPDATED);
                    this.TryUpdateDashboardTasks(
                        dispatch,
                        state.teamDashboard.tasks,
                        updatedTask,
                        ActionTypes.TEAM_DASHBOARD_TASKS_UPDATED);
                    this.TryUpdateDashboardTasks(
                        dispatch,
                        state.resourceDashboard.tasks,
                        updatedTask,
                        ActionTypes.RESOURCE_DASHBOARD_TASKS_UPDATED);
                })
                .catch(error => {
                    Logger.error(error);
                    dispatch({
                        type: ActionTypes.TASK_UPDATE_REQUEST_ERROR
                    });
                });
            
            dispatch({
                type: ActionTypes.REQUEST_UPDATE_TASK
            });
        });
    }
    private TryUpdateDashboardTasks(dispatch: any, tasks: Task[], task: Task, actionType: string) {
        const index = tasks
            .findIndex(t => t.id === task.id && t.deltekProjectNumber === task.deltekProjectNumber);

        if (index >= 0) {

            const newTasks = tasks.slice();
            newTasks.splice(index, 1, task);

            dispatch({
                tasks: newTasks,
                type: actionType               
            });
        }
    }

    private CloseModalDialog(dispatch: any) {
        dispatch({
            type: ActionTypes.CLOSE_MODAL_DIALOG
        });
    }
}

const actionCreators = new ActionCreators();
export default actionCreators;


