import { connect } from 'react-redux';

import actionCreators from '../../actions/ProjectsDashboard/ActionCreators';
import UsersDropdown from '../../components/Users/UsersDropdown/UsersDropdown';
import { IApplicationState } from '../../store';

const mapStateToProps = (state: IApplicationState) => {
    return {
        selectedUser: state.projectsDashboard.user,
        users: state.common.allUsers
    }
}

export default connect(mapStateToProps,
    {
        onUserSelected: actionCreators.UserSelected.bind(actionCreators)
    })(UsersDropdown);


