import * as React from 'react';

import Project from '../../../models/Project';
import SearchableList from '../../SearchableList/SearchableList';
import ProjectShortInfo from '../ProjectShortInfo/ProjectShortInfo';
import { IProjectsSearchableListProps } from './ProjectsSearchableListProps';

export default class ProjectsSearchableList extends React.PureComponent<IProjectsSearchableListProps> {
    constructor(props: IProjectsSearchableListProps) {
        super(props);
        this.valueExtractor = this.valueExtractor.bind(this);
        this.match = this.match.bind(this);
    }

    public render() {
        type SearchableProjectsListType = new () => SearchableList<Project>;
        const SearchableProjectsList = SearchableList as SearchableProjectsListType;

        return <SearchableProjectsList
            items={this.props.projects}
            selectedItem={this.props.selectedProject}
            onItemSelected={this.props.onProjectSelected}
            match={this.match}
            valueExtractor={this.valueExtractor}
            renderItem={ProjectShortInfo}
            title="Projects" />
    }

    public valueExtractor(project: Project) {
        return project.id.toString();
    }

    public match(request: string, project: Project): boolean {
        return project.deltekProjectNumber.indexOf(request) > -1 ||
            project.projectName.indexOf(request) > -1
    }
}


