import { Logger } from '@pnp/logging';

import api from '../../api/api';
import Consts from '../../Consts';
import Role from '../../models/Role';
import SPLookupValue from '../../models/SPLookupValue';
import Task from '../../models/Task';
import { AppThunkAction, IApplicationState } from '../../store';
import ActionTypes from '../ActionTypes';
import AddTasksActionsCreatorBase from '../AddTasksActionsCreatorBase';
import { AddSingleTaskAction, RolesByProjectAction } from './IActions';

class ActionCreators extends AddTasksActionsCreatorBase {

    public ProjectSelected(deltekProjectNumber: string): AppThunkAction<RolesByProjectAction> {
        return ((dispatch: any, getState: any) => {
            if (!deltekProjectNumber || deltekProjectNumber === '') {
                this.ResetRolesByProject(dispatch);
            } else {
                this.RequestRolesByProject(dispatch, deltekProjectNumber);
            }
        });
    }

    public AddSingleTask(addSingleTaskForData: any): AppThunkAction<AddSingleTaskAction> {
        return ((dispatch: any, getState: any) => {
            const state = (getState() as IApplicationState);
            const roles = state.addSingleTaskFormState.rolesByProject;

            const newTask = this.ToTask(roles, addSingleTaskForData, state.common.loggedUser);
            api.AddTasks(addSingleTaskForData.deltekProjectNumber, newTask)
                .then(tasks => {
                    dispatch({
                        type: ActionTypes.ADD_SINGLE_TASK_REQUEST_SUCCESS
                    });

                    this.CloseModalDialog(dispatch);
                    this.TryUpdateProjectsDashboardTasks(dispatch, state.projectsDashboard, ...tasks);
                    this.TryUpdateTeamDashboardTasks(dispatch, state.teamDashboard, ...tasks);
                })
                .catch(error => {
                    Logger.error(error);
                    dispatch({
                        type: ActionTypes.ADD_SINGLE_TASK_REQUEST_ERROR
                    });
                });

                dispatch({
                    type: ActionTypes.ADD_SINGLE_TASK_REQUEST
                });
        });
    }
    private CloseModalDialog(dispatch: any) {
        dispatch({
            type: ActionTypes.CLOSE_MODAL_DIALOG
        });
    }

    private ToTask(roles: Role[], addSingleTaskFormData: any, currentUser: SPLookupValue | null): Task {
        const task = new Task();
        const assignedTo = roles.find(r => r.name === addSingleTaskFormData.assignedToRoleName);
        if (assignedTo) {
            task.assignedTo = assignedTo.users;
        }
        task.name = addSingleTaskFormData.taskName;
        task.due = addSingleTaskFormData.dueDateUtc;
        task.phase = addSingleTaskFormData.phase;
        task.status = Consts.TaskStatuces.NotStarted;
        task.assignedBy = currentUser;

        return task;
    }

    private ResetRolesByProject(dispatch: any)
    {
        dispatch({
            type: ActionTypes.RESET_ROLES_BY_PROJECT
        });
    }

    private RequestRolesByProject(dispatch: any, deltekProjectNumber: string)
    {
        api.Roles(deltekProjectNumber)
            .then(roles => {
                dispatch({
                    roles,
                    type: ActionTypes.RECEIVE_ROLES_BY_PROJECT                    
                });
            })
            .catch(error => {
                Logger.error(error);
                dispatch({
                    type: ActionTypes.ROLES_BY_PROJECT_REQUEST_ERROR
                });
            });
        dispatch({
            type: ActionTypes.REQUEST_ROLES_BY_PROJECT
        });
    }
}

const actionCreators = new ActionCreators();
export default actionCreators;



