import * as React from 'react';

import { ITaskEditLinkTitleProps } from './TaskEditLinkTitleProps';

export default class TaskEditLinkTitle extends React.PureComponent<ITaskEditLinkTitleProps> {
    constructor(props: ITaskEditLinkTitleProps) {
        super(props);
        this.onClick = this.onClick.bind(this);
    }

    public onClick() {
        this.props.onClick(this.props.task);
    }

    public render() {
        return <div>
            <a style={{ cursor: 'pointer' }}
                onClick={this.onClick}>
                {this.props.task.name}
            </a>
        </div>
    }
}


