import { Action, Reducer } from 'redux';

import ActionTypes from '../../actions/ActionTypes';
import * as Actions from '../../actions/ProjectsDashboard/IActions';
import ProjectsDashboardState from '../../store/Dashboards/ProjectsDashboardState';

const initialState = {
    projects: [],
    projectsRequest: false,
    projectsRequestError: false,
    
    user: null,

    roles: [],

    project: null,
    projectDataRequest: false,
    projectDataRequestError: false,
    tasks: [],
}

export const ProjectsDashboardReducer: Reducer<ProjectsDashboardState> = (state: ProjectsDashboardState = initialState, incomingAction: Action) => {
    const action = incomingAction as Actions.ProjectDataAction
        | Actions.ProjectsByUserAction
        | Actions.IProjectsDashboardProjectSelectedAction
        | Actions.IProjectsDashboardResetUserDataAction
        | Actions.IProjectsDashboardTasksUpdatedAction
        | Actions.IProjectsDashboardUserSelectedAction;

    switch (action.type) {
        case ActionTypes.PROJECTS_DASHBOARD_PROJECT_SELECTED:
        {
            return {
                ...state,
                project: action.project
            };
        }
        case ActionTypes.PROJECTS_DASHBOARD_USER_SELECTED:
        {
            return {
                ...state,
                user: action.user
            };
        }
        case ActionTypes.PROJECTS_DASHBOARD_RESET_USER_DATA:
            {
                return {
                    ...state,
                    assignedByTasks: [],
                    assignedToTasks: [],
                    project: null,
                    roles: []
                };
            }
        case ActionTypes.REQUEST_PROJECTS_BY_USER:
            {
                return {
                    ...state,
                    projectsRequest: true
                };
            }
        case ActionTypes.PROJECTS_BY_USER_REQUEST_ERROR:
            {
                return {
                    ...state,
                    projectsRequest: true,
                    projectsRequestError: true
                };
            }
        case ActionTypes.RECEIVE_PROJECTS_BY_USER:
            {
                return {
                    ...state,
                    projects: action.projects,
                    projectsRequest: false,
                    projectsRequestError: false                    
                };
            }
        case ActionTypes.REQUEST_PROJECT_DATA:
            {
                return {
                    ...state,
                    projectDataRequest: true
                };
            }
        case ActionTypes.PROJECT_DATA_REQUEST_ERROR:
            {
                return {
                    ...state,
                    projectDataRequest: false,
                    projectDataRequestError: true
                };
            }
        case ActionTypes.RECEIVE_PROJECT_DATA:
            {
                return {
                    ...state,
                    projectDataRequest: false,
                    projectDataRequestError: false,
                    roles: action.roles,
                    tasks: action.tasks
                };
            }
        case ActionTypes.PROJECTS_DASHBOARD_TASKS_UPDATED:
            {
                return {
                    ...state,
                    tasks: action.tasks
                };
            }
        default: {
            return state;
        }
    }
};


