import * as React from "react";
import { ILoggedUserInfoProps } from "./LoggedUserInfoProps";

export default class AADUserInfo extends React.PureComponent<ILoggedUserInfoProps> {
    public render() {
        if (this.props.user) {
            return <span>
                <strong>{this.props.user.displayName}</strong>
            </span>;
        }
        return null;
    }
}


