import * as React from 'react';
import SPUser from '../../../models/SPUser';
import MultipleSelect from '../../MultipleSelect/MultipleSelect';
import { IUsersMultipleSelectProps } from './UsersMultipleSelectProps';

export default class UsersMultipleSelect extends React.PureComponent<IUsersMultipleSelectProps> {
    constructor(props: IUsersMultipleSelectProps) {
        super(props);

        this.userTitleExtractor = this.userTitleExtractor.bind(this);
        this.userValueExtractor = this.userValueExtractor.bind(this);
        this.userGroupBy = this.userGroupBy.bind(this);
    }

    public userTitleExtractor(user: SPUser) {
        return user.displayName;
    }

    public userValueExtractor(user: SPUser) {
        return user.id.toString();
    }

    public userGroupBy(user: SPUser) {
        return user.office;
    }

    public render() {
        type UsersMultipleSelectType = new () => MultipleSelect<SPUser>;
        const MultipleSelectUsers = MultipleSelect as UsersMultipleSelectType;

        return <MultipleSelectUsers
            items={this.props.users}
            onSelectionChanged={this.props.onSelectionChanged}
            selectedItems={this.props.selectedUsers}
            title="Users"
            id="users-multiple-select"
            selectedItemsAreaVisible={true}
            titleExtractor={this.userTitleExtractor}
            valueExtractor={this.userValueExtractor}
            groupBy={this.userGroupBy} />
    }
}


