import Consts from '../Consts';
import Role from '../models/Role';
import Repo from './Repo';

export default class RolesRepo extends Repo {
    private consts = Consts.RolesListConsts;

    private fields = [
        this.consts.Fields.RoleNameFieldName,
        this.LookupIdAndTitle(this.consts.Fields.UsersFieldName),
        'Id'
    ]

    private expand = [
        this.consts.Fields.UsersFieldName
    ]

    public GetAll(deltekProjectNumber: string): Promise<Role[]> {

        const web = this.GetProjectWeb(deltekProjectNumber);

        return web.lists.getByTitle(this.consts.ListTitle).items
            .select(this.fields.join(','))
            .expand(this.expand.join(','))
            .getAll()
            .then(items => items.map(i => this.ToRole(i)));
    }

    private ToRole(item: any): Role {
        const role: Role = new Role();
        role.id = item.Id;
        role.name = item[this.consts.Fields.RoleNameFieldName];
        role.users = this.ToLookupMulti(item[this.consts.Fields.UsersFieldName]);

        return role;
    }
}


