import * as React from 'react';
import {
    Button, ButtonToolbar, Col, ControlLabel, Form, FormControl, FormGroup
} from 'react-bootstrap';

import Consts from '../../../Consts';
import DateTime from '../../DateTime/DateTime';
import UrlLink from '../../UrlLink/UrlLink';
import { ITaskEditFormProps } from './TaskEditFormProps';
import { ITaskEditFormState } from './TaskEditFormState';

export default class TaskEditForm extends React.PureComponent<ITaskEditFormProps, ITaskEditFormState> {
    constructor(props: ITaskEditFormProps) {
        super(props);

        this.state = {
            deleverableLink: props.task.deliverableLinkUrl,
            delivarableLinkValid: true,
            dueDateUtc: props.task.due,
            dueDateValid: true,
            status: props.task.status
        }
        
        this.save = this.save.bind(this);
        
        this.onStatusUpdated = this.onStatusUpdated.bind(this);
        this.onDeliverableLinkUpdated = this.onDeliverableLinkUpdated.bind(this);
        this.onDueDateUpdated = this.onDueDateUpdated.bind(this);
    }

    get valid(): boolean {
        return this.state.delivarableLinkValid && this.state.dueDateValid;
    }

    public save() {
        if (this.valid) {
            const formData: any = {};
            formData.deliverableLinkUrl = this.state.deleverableLink,
            formData.due = this.state.dueDateUtc,
            formData.status = this.state.status,

            this.props.onUpdate(this.props.task, formData);
        }
    }

    public onStatusUpdated(e: any) {
        this.setState({
            ...this.state,
            status: e.target.value
        });
    }

    public onDeliverableLinkUpdated(newValue: string, isValid: boolean) {
        this.setState({
            ...this.state,
            deleverableLink: newValue,
            delivarableLinkValid: isValid
        });
    }

    public onDueDateUpdated(newDateUtc: string, isValid: boolean) {
        this.setState({
            ...this.state,
            dueDateUtc: newDateUtc,
            dueDateValid: isValid
        });
    }

    public render() {
        return <Form horizontal={true}>
            <FormGroup>
                <Col componentClass={ControlLabel} sm={3}>
                    Name
                </Col>
                <Col sm={9}>
                    <FormControl.Static>{this.props.task.name}</FormControl.Static>
                </Col>
            </FormGroup>
            <FormGroup>
                <Col componentClass={ControlLabel} sm={3}>
                    Assigned To
                        </Col>
                <Col sm={9}>
                    <FormControl.Static>
                        {this.props.task.assignedToStr}
                    </FormControl.Static>
                </Col>
            </FormGroup>
            <FormGroup>
                <Col componentClass={ControlLabel} sm={3}>
                    Assigned By
                        </Col>
                <Col sm={9}>
                    <FormControl.Static>{this.props.task.assignedByStr}</FormControl.Static>
                </Col>
            </FormGroup>
            <FormGroup>
                <Col componentClass={ControlLabel} sm={3}>
                    Status
                        </Col>
                <Col sm={9}>
                    <FormControl componentClass="select"
                        onChange={this.onStatusUpdated}
                        defaultValue={this.state.status}
                        placeholder="select">
                        {
                            (Object as any).values(Consts.TaskStatuces).map((status: any, i: any) => {
                                return <option key={i} value={status}>{status}</option>
                            })
                        }
                    </FormControl>
                </Col>
            </FormGroup>
            <FormGroup validationState={this.state.dueDateValid ? null : 'error'}>
                <Col componentClass={ControlLabel} sm={3}>
                    Due
                </Col>
                <Col sm={9}>
                    <DateTime
                        onUpdated={this.onDueDateUpdated}
                        initialDate={this.state.dueDateUtc}
                        timeFormat={Consts.TimeFormat}
                        dateFormat={Consts.DateFormat} />
                </Col>
            </FormGroup>
            <FormGroup
                validationState={this.state.delivarableLinkValid ? null : 'error'}>
                <Col componentClass={ControlLabel} sm={3}>
                    Path to the deliverable
                </Col>
                <Col sm={9}>
                    <UrlLink
                        initialValue={this.state.deleverableLink}
                        onUpdated={this.onDeliverableLinkUpdated} />
                </Col>
            </FormGroup>
            <FormGroup>
                <Col sm={12}>
                    <ButtonToolbar className="pull-right">
                        <Button
                            disabled={!this.valid}
                            bsStyle="success"
                            onClick={this.save}>Save</Button>
                    </ButtonToolbar>
                </Col>
            </FormGroup>
        </Form>;
    }
}


