import * as React from 'react';

import { IProjectFullInfoProps } from './ProjectFullInfoProps';

const ProjectFullInfo: React.SFC<IProjectFullInfoProps> = (props: IProjectFullInfoProps) => {
    return <div className="card">
        <div className="card-body">
            <a target="_blank" href={props.project.siteUrl}>
                <h4 className="card-title">{props.project.projectName}</h4>
            </a>
            <h5 className="card-subtitle mb-2 text-muted">{props.project.deltekProjectNumber}</h5>
            <p className="card-text">
                <b>Principal:</b>
                {props.project.principal !== null ? props.project.principal.displayName : ''}
            </p>
            <p className="card-text">
                <b>Project Manager:</b>
                {props.project.projectManager !== null ? props.project.projectManager.displayName : ''}
            </p>
            <p className="card-text"><b>Profit Center:</b> {props.project.profitCenter}</p>
        </div>
    </div>
}
export default ProjectFullInfo;



