import { Action, Reducer } from 'redux';

import ActionTypes from '../../actions/ActionTypes';
import * as Actions from '../../actions/TeamDashboard/IActions';
import TeamDashboardState from '../../store/Dashboards/TeamDashboardState';

const initialState = {
    activeUsers: [],
    activeUsersRequest: false,
    activeUsersRequestError: false,

    activeUsersRequested: false,
    
    offices: [],

    tasks: [],
    tasksRequest: false,
    tasksRequestError: false,

    user: null
}

export const TeamDashboardReducer: Reducer<TeamDashboardState> = (state: TeamDashboardState = initialState, incomingAction: Action) => {
    const action = incomingAction as Actions.TasksByUserAction
        | Actions.ActiveUsersAction
        | Actions.ITeamDashboardSelectedOfficesUpdatedAction
        | Actions.ITeamDashboardUserSelectedAction
        | Actions.ITeamDashboardTasksUpdatedAction

    switch (action.type) {
        case ActionTypes.REQUEST_ACTIVE_USERS:
            {
                return {
                    ...state,
                    activeUsersRequest: true,
                };
            }
        case ActionTypes.ACTIVE_USERS_REQUEST_ERROR:
            {
                return {
                    ...state,
                    activeUsersRequest: false,
                    activeUsersRequestError: true,
                    activeUsersRequested: true
                };
            }
        case ActionTypes.RECEIVE_ACTIVE_USERS:
            {
                return {
                    ...state,
                    activeUsers: action.usersIds,
                    activeUsersRequest: false,
                    activeUsersRequestError: false,
                    activeUsersRequested: true                    
                };
            }
        case ActionTypes.REQUEST_TASKS_BY_USER:
            {
                return {
                    ...state,
                    tasksRequest: true
                };
            }
        case ActionTypes.TASKS_BY_USER_REQUEST_ERROR:
            {
                return {
                    ...state,
                    tasksRequest: false,
                    tasksRequestError: true
                };
            }
        case ActionTypes.RECEIVE_TASKS_BY_USER:
            {
                return {
                    ...state,
                    tasks: action.tasks,
                    tasksRequest: false,
                    tasksRequestError: false                    
                };
            }
        case ActionTypes.TEAM_DASHBOARD_OFFICES_UPDATED:
            {
                return {
                    ...state,
                    offices: action.offices
                };
            }
        case ActionTypes.TEAM_DASHBOARD_TASKS_UPDATED:
        {
            return {
                ...state,
                tasks: action.tasks
            };
        }
        case ActionTypes.TEAM_DASHBOARD_USER_SELECTED:
        {
            return {
                ...state,
                user: action.user
            };
        }
        default: {
            return state;
        }
    }
};


