import * as React from 'react';
import { connect } from 'react-redux';

import actionCreators from '../../actions/RootModal/ActionCreators';
import modal from '../../components/Modal/Modal';
import ModalDialogs from '../../ModalDialogs';
import { IApplicationState } from '../../store';

export interface IModalRootProps {
    modal: React.ComponentType | null;
    modalProps: any;
    title: string;
    onClose: () => any
}

const mapStateToProps = (state: IApplicationState) => {
    return {
        modal: ModalDialogs.Get(state.rootModalDialogState.modalType),
        modalProps: state.rootModalDialogState.modalProps,
        title: state.rootModalDialogState.modalTitle
    }
}

const RootModalContainer: React.SFC<IModalRootProps> = (props: IModalRootProps) => {
    if (props.modal) {
        const Component = modal(props.modal, {title: props.title}, props.onClose);
        return <Component {...props.modalProps} />
    } return null;
}

export default connect(
    mapStateToProps, { 
        onClose: actionCreators.CloseModalDialog.bind(actionCreators) 
    })(RootModalContainer);


