import * as React from 'react';
import { Col, Row } from 'react-bootstrap';

import EventsCalendarContainer from '../containers/ResourceDashboard/EventsCalendarContainer';
import ResourceFilterContainer from '../containers/ResourceDashboard/ResourceFilterContainer';

export default class ResourceDashboard extends React.Component<any, any> {
    public render() {
        return <Row>
            <Col sm={12}>
                <ResourceFilterContainer />
                <br/>
                <EventsCalendarContainer />
            </Col>
        </Row>
    }
}




