import './styles.css';

import * as moment from 'moment';
import * as React from 'react';
import BigCalendar from 'react-big-calendar';
import { Col } from 'react-bootstrap';

import CalendarEvent from '../../../models/CalendarEvent';
import EventType from '../../../models/EventType';
import Milestone from '../../../models/Milestone';
import Task from '../../../models/Task';
import MilestoneEvent from '../../Milestones/MilestoneEvent/MilestoneEvent';
import TaskEvent from '../../Tasks/TaskEvent/TaskEvent';
import { IEventsCalendarProps } from './EventsCalendarProps';

BigCalendar.momentLocalizer(moment);

const Legend: React.SFC = () => {
    return <div className='events-calendar-legend'>
            <Col sm={1} xs={2} className="bg-info"/>
            <Col sm={1} xs={3}>Tasks</Col>
            <Col sm={1} xs={2} className="bg-danger"/>
            <Col sm={1} xs={3}>Milestones</Col>
        </div>
}

export default class EventsCalendar extends React.PureComponent<IEventsCalendarProps> {
    constructor(props: IEventsCalendarProps) {
        super(props);
        this.renderEvent = this.renderEvent.bind(this);
        this.dateAccessor = this.dateAccessor.bind(this);
        this.allDayAccessor = this.allDayAccessor.bind(this);
        this.eventPropGetter = this.eventPropGetter.bind(this);
    }

    public render() {
        return <div className="events-calendar-container">
            <Legend />
            <br />
            <BigCalendar
                selectable={false}
                events={this.props.events.slice()}
                views={['month', 'week']}
                startAccessor={this.dateAccessor}
                endAccessor={this.dateAccessor}
                allDayAccessor={this.allDayAccessor}
                popup={true}
                components={{
                    event: this.renderEvent
                }}
                defaultDate={new Date()}
                eventPropGetter={this.eventPropGetter}
            />
        </div>
    }

    private dateAccessor (event: CalendarEvent) {
        return event.date;
    }

    private allDayAccessor (event: CalendarEvent) {
        return true
    }

    private eventPropGetter () {
        return { style: { backgroundColor: 'transparent' } }
    }

    private renderEvent(data: any) {
        const calendarEvent = data.event as CalendarEvent;
        switch (calendarEvent.type) {
            case EventType.Milestone: {
                return <MilestoneEvent
                    milestone={calendarEvent.entity as Milestone} />
            }
            case EventType.Task: {
                return <TaskEvent
                    onEdit={this.props.onTaskEdit}
                    task={calendarEvent.entity as Task} />
            }
            default: {
                return null;
            }
        }
    }
}


