import * as React from 'react';

import AddModulesFormContainer from './containers/Modal/AddModulesFormContainer';
import AddSingleTaskFormContainer from './containers/Modal/AddSingleTaskFormContainer';
import TaskEditFormContainer from './containers/Modal/TaskEditFormContainer';

export default class ModalDialogs {
    public static Get(modalType: string): React.ComponentType | null {
        switch (modalType) {
            case 'ADD_MODULES': {
                return AddModulesFormContainer;
            }
            case 'EDIT_TASK': {
                return TaskEditFormContainer;
            }
            case 'ADD_SINGLE_TASK': {
                return AddSingleTaskFormContainer;
            }
            default: {
                return null;
            }
        }
    }
}


