import { Action, Reducer } from 'redux';

import ActionTypes from '../../actions/ActionTypes';
import { ICloseDialogAction, IOpenDialogAction } from '../../actions/RootModal/IActions';
import RootModalDialogState from '../../store/Common/RootModalDialogState';

const initialState = {
    modalProps: null,
    modalTitle: '',
    modalType: '',
    modalVisible: false,
}

export const RootModalDialogReducer: Reducer<RootModalDialogState> = (state: RootModalDialogState = initialState, incomingAction: Action) => {
    const action = incomingAction as IOpenDialogAction
        | ICloseDialogAction;

    switch (action.type) {
        case ActionTypes.OPEN_MODAL_DIALOG:
            {
                return {
                    ...state,
                    modalProps: action.modalProps,
                    modalTitle: action.modalTitle,
                    modalType: action.modalType                 
                };
            }
        case ActionTypes.CLOSE_MODAL_DIALOG:
            {
                return {
                    ...state,
                    modalProps: null,
                    modalTitle: '',
                    modalType: ''
                };
            }
        default: {
            return state;
        }
    }
};


