import * as React from 'react';
import { Badge, Panel } from 'react-bootstrap';
import Responsive from 'react-responsive';

import Task from '../../../models/Task';
import { ITasksCollapsePanelProps } from './TasksCollapsePanelProps';

const DesktopScreen = (props: any) => <Responsive {...props} minWidth={992} />;
const MobileScreen = (props: any) => <Responsive {...props} maxWidth={991} />;

export default class TasksCollapsePanel extends React.PureComponent<ITasksCollapsePanelProps> {
    public renderTasks(tasks: Task[]) {
        return <div>
            <DesktopScreen>
                <div className="table-responsive">
                    <table className="table">
                        <thead>
                            <tr>
                                <th> Name </th>
                                <th> Assigned To </th>
                                <th> Assigned By </th>
                                <th> Status </th>
                                <th> Due </th>
                                <th/>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                tasks.map(task => (
                                    <this.props.renderTaskDesctop
                                        key={task.id}
                                        task={task}
                                        onEdit={this.props.onTaskEdit} />
                                ))
                            }
                        </tbody>
                    </table>
                </div>
            </DesktopScreen>
            <MobileScreen>
                <div>
                    {
                        tasks.map(task => (
                            <this.props.renderTaskMobile
                                key={task.id}
                                task={task}
                                onEdit={this.props.onTaskEdit} />
                        ))
                    }
                </div>
            </MobileScreen>
        </div>
    }

    public render() {
        const tasksView = this.renderTasks(this.props.tasks);
        
        return <div>
            <Panel defaultExpanded={this.props.defaultExpanded} bsStyle={this.props.bsStyle}>
                <Panel.Heading>
                    <Panel.Title toggle={true}>
                        {this.props.title}
                        <Badge pullRight={true}>{this.props.tasks.length}</Badge>
                    </Panel.Title>
                </Panel.Heading>
                <Panel.Collapse>
                    <Panel.Body>
                        {tasksView}
                    </Panel.Body>
                </Panel.Collapse>
            </Panel>
        </div>
    }
}


