import './styles.css';

import * as React from 'react';

import { IMilestoneEventProps } from './MilestoneEventProps';

const MilestoneEvent: React.SFC<IMilestoneEventProps> = (props: IMilestoneEventProps) => {
    return <div className="calendar-event-milestone bg-danger">
        <a target='_blank' href={props.milestone.titleUrl}>
            {props.milestone.title}
        </a>
        <br />
        {props.milestone.responsible !== null ? props.milestone.responsible.displayName : ''}
        <br />
        <a target='_blank' href={props.milestone.projectSiteUrl}>
            {props.milestone.deltekProjectNumber}
        </a>
    </div>;
}
export default MilestoneEvent;


