import { connect } from 'react-redux';

import LoggedUserInfo from '../../components/Users/LoggedUserInfo/LoggedUserInfo';
import { IApplicationState } from '../../store';

function mapStateToProps(state: IApplicationState) {
    return {
        user: state.common.loggedUser
    }
}

export default connect(
    mapStateToProps, {})(LoggedUserInfo);


