import { connect } from 'react-redux';

import actionCreators from '../../actions/AddSingleTaskForm/ActionCreators';
import AddSingleTaskForm from '../../components/AddSingleTaskForm/AddSingleTaskForm';
import { IApplicationState } from '../../store';

const mapStateToProps = (state: IApplicationState, ownProps: any) => {
    return {
        project: ownProps.project,
        projects: state.common.activeProjects,
        roles: state.addSingleTaskFormState.rolesByProject
    }
}

const mapDispatchToProps = {
    onAddSingleTask: actionCreators.AddSingleTask.bind(actionCreators),
    onProjectSelected: actionCreators.ProjectSelected.bind(actionCreators)
}

export default connect(
    mapStateToProps, mapDispatchToProps)(AddSingleTaskForm);


