import { Action, Reducer } from 'redux';

import ActionTypes from '../../actions/ActionTypes';
import UpdateTaskAction from '../../actions/UpdateTaskForm/IActions';
import UpdateTaskFormState from '../../store/Modals/UpdateTaskFormState';

const initialState = {
    updateTaskRequest: false,
    updateTaskRequestError: false
}

export const UpdateTaskFormReducer: Reducer<UpdateTaskFormState> = (state: UpdateTaskFormState = initialState, incomingAction: Action) => {
    const action = incomingAction as UpdateTaskAction;

    switch (action.type) {
        case ActionTypes.REQUEST_UPDATE_TASK:
            {
                return {
                    ...state,
                    updateTaskRequest: true
                };
            }
        case ActionTypes.TASK_UPDATE_REQUEST_ERROR:
            {
                return {
                    ...state,
                    updateTaskRequest: false,
                    updateTaskRequestError: true
                };
            }
        case ActionTypes.TASK_UPDATE_REQUEST_SUCCESS:
            {
                return {
                    ...state,
                    updateTaskRequest: false,
                    updateTaskRequestError: false
                };
            }
        default: {
            return state;
        }
    }
};


