import './styles.css';

import * as React from 'react';

import { ITaskViewProps } from '../ITaskViewProps';
import TaskEditLinkTitle from '../TaskEditLinkTitle/TaskEditLinkTitle';

const TaskEvent: React.SFC<ITaskViewProps> = (props: ITaskViewProps) => {
    return <div className="calendar-event-task bg-info">
            <TaskEditLinkTitle
                task={props.task}
                onClick={props.onEdit} />
            {props.task.assignedToStr}
            <br />
            <a target='_blank' href={props.task.projectSiteUrl}>
                {props.task.deltekProjectNumber}
            </a>
        </div>;
}
export default TaskEvent;


