import * as moment from 'moment';
import * as React from 'react';
import * as Datetime from 'react-datetime';

import { IDateTimeProps } from './DateTimeProps';

export default class DateTime extends React.PureComponent<IDateTimeProps> {
    constructor(props: IDateTimeProps) {
        super(props);

        this.state = {
            currentDate: props.initialDate
        }

        this.onUpdated = this.onUpdated.bind(this);
    }

    public onUpdated(newDate: any) {
        const newDateLocal = moment(newDate);
        const newDateUtc = newDateLocal.utc();
        const dateTimeFormat = `${this.props.dateFormat} ${this.props.timeFormat}`
        this.props.onUpdated(newDateUtc.format(dateTimeFormat), newDateUtc.isValid());
    }

    public render() {
        return <Datetime defaultValue={moment.utc(this.props.initialDate).local()}
            timeFormat={this.props.timeFormat}
            onChange={this.onUpdated}
            dateFormat={this.props.dateFormat}
            inputProps={{ placeholder: 'N/A' }} />;
    }
}


