import './styles.css';

import * as React from 'react';
import { Button, Dropdown, ListGroupItem, Panel, Well } from 'react-bootstrap';

import { IMultipleSelectProps } from './MultipleSelectProps';
import { IMultipleSelectState } from './MultipleSelectState';

export default class MultipleSelect<T> extends React.PureComponent<IMultipleSelectProps<T>, IMultipleSelectState> {
    constructor(props: IMultipleSelectProps<T>) {
        super(props);

        this.selected = this.selected.bind(this);
        this.onSelected = this.onSelected.bind(this);
        this.renderSelectedItemsArea = this.renderSelectedItemsArea.bind(this);
        this.onSelectAll = this.onSelectAll.bind(this);
        this.renderItems = this.renderItems.bind(this);
        this.renderGroupedItems = this.renderGroupedItems.bind(this);

        this.state = { 
            selectAll: false,
            selectedItemsValues: props.selectedItems
                .map(i => props.valueExtractor(i))
        }
    }

    public componentWillReceiveProps(nextProps: IMultipleSelectProps<T>) {
        this.setState({
            selectAll: false,
            selectedItemsValues: nextProps.selectedItems
                .map(i => nextProps.valueExtractor(i))
        });
    }

    public render() {
        const selectedItems = this.renderSelectedItemsArea();

        let content = null;

        if (this.props.groupBy) {
            content = this.renderGroupedItems();
        } else {
            content = this.renderItems(this.props.items);
        }

        return <div>
            <div hidden={!this.props.selectedItemsAreaVisible}>
                {selectedItems}
            </div>
            <Dropdown id={this.props.id} className="multiple-select-dropdown">
                <Dropdown.Toggle className="form-control control-label multiple-select-dropdown-title">
                    {this.props.title}
                </Dropdown.Toggle>
                <Dropdown.Menu className="super-colors">
                    <div hidden={!this.props.selectAllButtonVisible}>
                        <div className="list-group-item">
                            <Button bsStyle="primary"
                                onClick={this.onSelectAll}
                                disabled={this.props.items.length === 0}
                                active={this.state.selectAll}>
                                Select/Unselect All
                        </Button>
                        </div>
                    </div>
                    {content}
                </Dropdown.Menu>
            </Dropdown>
        </div>
    }

    private selected(value: string): boolean {
        return this.state.selectedItemsValues
            .indexOf(value) >= 0;
    }

    private onSelected(e: any) {
        const value = e.currentTarget.value;
        const selectdItemIndex = this.state.selectedItemsValues
            .indexOf(value);
        const selectedItemsValues = this.state.selectedItemsValues
            .slice();

        if (selectdItemIndex >= 0) {
            // already selected
            selectedItemsValues.splice(selectdItemIndex, 1);
        } else {
            // not selected
            selectedItemsValues.push(value);
        }
        this.setState({
            ...this.state,
            selectedItemsValues
        });

        const selectedItems =  this.props.items
            .filter(i => selectedItemsValues
                .indexOf(this.props.valueExtractor(i)) >= 0);
                
        this.props.onSelectionChanged(selectedItems);
    }

    private renderSelectedItemsArea() {
        const selectedItemsTitles = this.props.items
            .filter(i => this.state.selectedItemsValues
                    .indexOf(this.props.valueExtractor(i)) >= 0)
            .map(item => this.props.titleExtractor(item)).join(', ');

        if (selectedItemsTitles.length > 0) {
            return <Well className="multiple-select-well"> { selectedItemsTitles  }
            </Well>
        } else {
            return <Well className="multiple-select-well">No selected items</Well>;
        }
    }

    private onSelectAll() {
        let selectAll: boolean = false;
        let selectedItemsValues: string[] = [];

        if (!this.state.selectAll) {
            // need select all
            selectAll = true;
            selectedItemsValues = this.props.items
                .map(i => this.props.valueExtractor(i));
        }

        this.setState({
            ...this.state,
            selectAll,
            selectedItemsValues           
        });

        this.props.onSelectionChanged(this.props.items);
    }

    private renderItems(items: T[]) {
        return <div className="list-group">
            {
                items.map(item => {
                    const value = this.props.valueExtractor(item);

                    return <ListGroupItem
                                key={value} 
                                value={value}
                                onClick={this.onSelected}>
                                <input
                                    role="button"
                                    type="checkbox"
                                //   onChange={e => this.onSelected(e, item)}
                                    checked={this.selected(value)} />
                                &nbsp;
                                <label role="button">
                                    {this.props.titleExtractor(item)}
                                </label>
                            </ListGroupItem>
                })
            }
        </div>
    }

    private renderGroupedItems() {
        const groups = this.props.items.reduce((g: any, item: T) => {
            if (this.props.groupBy) {
                const val = this.props.groupBy(item);
                g[val] = g[val] || [];
                g[val].push(item);
            }
            return g;
        }, {});

        return Object.keys(groups).map((key: any) =>
            <Panel key={key}>
                <Panel.Heading>
                    <Panel.Title toggle={true}>
                        {key}
                    </Panel.Title>
                </Panel.Heading>
                <Panel.Collapse>
                    <Panel.Body>
                        {this.renderItems(groups[key])}
                    </Panel.Body>
                </Panel.Collapse>
            </Panel>);
    }


}


