import './styles.css';

import * as React from 'react';
import { Tab, Tabs } from 'react-bootstrap';

import Roles from '../../Roles/Roles';
import ProjectActions from '../ProjectActions/ProjectActions';
import ProjectFullInfo from '../ProjectFullInfo/ProjectFullInfo';
import { IProjectCardProps } from './ProjectCardProps';

export default class ProjectCard extends React.PureComponent<IProjectCardProps> {
    public render() {
        if (this.props.project != null) {
            return <Tabs defaultActiveKey={1} id="uncontrolled-tab-example">
                <Tab eventKey={1} title="General" className="project-card-tab">
                    <ProjectFullInfo project={this.props.project} />
                    <ProjectActions
                        project={this.props.project}
                        onAddModules={this.props.onAddModules}
                        onAddSingleTask={this.props.onAddSingleTask} />
                </Tab>
                <Tab eventKey={2} title="Team" className="project-card-tab">
                    <Roles roles={this.props.roles} />
                </Tab>
            </Tabs>
        } else {
            return null;
        }
    }
}


