import * as moment from 'moment';

import SPLookupValue from './SPLookupValue';

export default class Milestone {
    public id: number = 0;
    public eventDate: string = '';
    public title: string = '';
    public titleUrl: string = '';

    public deltekProjectNumber: string = '';
    public projectSiteUrl: string = '';
    public responsible: SPLookupValue | null = null;

    public get eventDateLocal(): moment.Moment {
        const eventDateLocal = moment.utc(this.eventDate);
        return eventDateLocal.local();
    }
}



