import { connect } from 'react-redux';

import actionCreators from '../../actions/UpdateTaskForm/ActionCreators';
import TaskEditForm from '../../components/Tasks/TaskEditForm/TaskEditForm';

export default connect(
    null, { 
        onUpdate: actionCreators.UpdateTask.bind(actionCreators)
    })(TaskEditForm);


