import * as React from 'react';
import { Button, ButtonToolbar } from 'react-bootstrap';

import { IProjectActionsProps } from './ProjectActionsProps';

export default class ProjectActions extends React.PureComponent<IProjectActionsProps> {

    constructor(props: IProjectActionsProps) {
        super(props);

        this.onAddModules = this.onAddModules.bind(this);
        this.onAddSingleTask = this.onAddSingleTask.bind(this);
    }

    public onAddModules() {
        this.props.onAddModules(this.props.project);
    }

    public onAddSingleTask() {
        this.props.onAddSingleTask(this.props.project);
    }

    public render() {
        return <ButtonToolbar>
            <Button bsStyle="primary" onClick={this.onAddModules}>Add Module</Button>
            <Button bsStyle="primary" onClick={this.onAddSingleTask}>Add Single Task</Button>
        </ButtonToolbar>
    }
}


