import * as React from 'react';

import { ITaskViewProps } from '../ITaskViewProps';
import TaskEditLinkTitle from '../TaskEditLinkTitle/TaskEditLinkTitle';
import TaskLinks from '../TaskLinks/TaskLinks';

const TaskDesktop: React.SFC<ITaskViewProps> = (props: ITaskViewProps) => {
    return <tr key={props.task.id}>
            <td>
                <TaskEditLinkTitle
                    task={props.task}
                    onClick={props.onEdit} />

                <a href={props.task.projectSiteUrl}
                    target="_blank"
                    className="btn-link">{props.task.deltekProjectNumber}</a>
            </td>
            <td>{props.task.assignedToStr}</td>
            <td>{props.task.assignedByStr}</td>
            <td>{props.task.status}</td>
            <td>{props.task.dueLocalStr}</td>
            <td>
                <TaskLinks task={props.task} />
            </td>
        </tr>
}
export default TaskDesktop;


