import * as moment from 'moment';
import * as React from 'react';
import {
    Button, ButtonToolbar, Checkbox, ControlLabel, Form, FormControl, FormGroup
} from 'react-bootstrap';

import Consts from '../../Consts';
import Project from '../../models/Project';
import DateTime from '../DateTime/DateTime';
import StringMultipleSelect from '../StringMultipleSelect/StringMultipleSelect';
import { IAddModulesFormProps } from './AddModulesFormProps';
import { IAddModulesFormState } from './AddModulesFormState';

export default class AddModulesForm extends React.PureComponent<IAddModulesFormProps, IAddModulesFormState> {
    constructor(props: IAddModulesFormProps) {
        super(props);

        let phase = '';
        let phases: string[] = [];
        let deltekProjectNumber = '';
        const project = props.project;

        if (project !== null) {
            deltekProjectNumber = project.deltekProjectNumber;
            if (project.phases.length > 0) {
                phase = project.phases[0].displayName;
                phases = project.phases
                    .map(p => p.displayName);
            }
        }

        this.state = {
            associatedToMilestone: true,
            deltekProjectNumber,
            dueDateUtc: moment().utc().format(Consts.DateTimeFormat),
            dueDateValid: true,
            modules: [],
            phase,
            phases,
            projectTemplateUrl: ''
        }
        
        this.associatedToMilestoneChanged = this.associatedToMilestoneChanged.bind(this);
        this.onModulesSelectionChanged = this.onModulesSelectionChanged.bind(this);
        this.onProjectTemplateSelected = this.onProjectTemplateSelected.bind(this);
        this.onProjectPhaseSelected = this.onProjectPhaseSelected.bind(this);
        this.onProjectSelected = this.onProjectSelected.bind(this);
        this.onDueDateUpdated = this.onDueDateUpdated.bind(this);
        this.onAddModules = this.onAddModules.bind(this);
    }

    public onDueDateUpdated(newDate: string, isValid: boolean) {
        this.setState(
            {
                ...this.state,
                dueDateUtc: newDate,
                dueDateValid: isValid                
            });
    }

    public associatedToMilestoneChanged() {
        this.setState(
            {
                ...this.state,
                associatedToMilestone: !this.state.associatedToMilestone
            });
    }

    public onModulesSelectionChanged(modules: string[]) {
        this.setState(
            {
                ...this.state,
                modules
            });
    }

    public onProjectSelected(e: any) {
        const deltekProjectNumber = e.target.value;
        let phases: string[] = [];
        let phase: string = '';

        if (deltekProjectNumber !== '') {
            const project = this.props.projects
                .find(p => p.deltekProjectNumber === deltekProjectNumber);

            if (project) {
                phases = (project as Project).phases
                    .map(p => p.displayName);

                if (phases.length > 0) {
                    phase = phases[0];
                }
            }
        }

        this.setState(
            {
                ...this.state,
                deltekProjectNumber,
                phase,
                phases
            });
    }

    public onProjectTemplateSelected(e: any) {
        const projectTemplateUrl = e.target.value;

        this.setState(
            {
                ...this.state,
                modules: [],
                projectTemplateUrl                
            });

        this.props.onProjectTemplateSelected(projectTemplateUrl);
    }

    public onProjectPhaseSelected(e: any) {
        const phase = e.target.value;
        this.setState(
            {
                ...this.state,
                phase
            });
    }

    public onAddModules() {
        if (this.valid) {
            const formData = {
                deltekProjectNumber: this.state.deltekProjectNumber,
                dueDateUtc: this.state.dueDateUtc,
                modules: this.state.modules,
                phase: this.state.associatedToMilestone ? this.state.phase : ''
            };

            this.props.onAddModules(formData);
        }
    }

    get valid(): boolean {
        return this.state.dueDateValid
            && this.state.deltekProjectNumber !== ''
            && this.state.modules.length > 0;
    }

    public render() {
        return <Form>
            <FormGroup validationState={this.state.deltekProjectNumber === '' ? 'error' : null}>
                <ControlLabel>
                    Select a Project
                </ControlLabel>
                <FormControl componentClass="select"
                    defaultValue={this.state.deltekProjectNumber}
                    onChange={this.onProjectSelected}
                    placeholder="select">
                    <option />
                    {this.props.projects.map(p =>
                        <option key={p.id} value={p.deltekProjectNumber}>{p.projectName}</option>)}
                </FormControl>
            </FormGroup>
            <FormGroup>
                <ControlLabel>
                    <Checkbox
                        inline={true}
                        checked={this.state.associatedToMilestone}
                        onChange={this.associatedToMilestoneChanged}>
                        <strong>Associated to Milestone</strong>
                        </Checkbox>
                </ControlLabel>
                <FormControl componentClass="select"
                    disabled={!this.state.associatedToMilestone}
                    onChange={this.onProjectPhaseSelected}
                    defaultValue={this.state.phase}
                    placeholder="select">
                    {this.state.phases
                            .map((p, i) => <option key={i} value={p}>{p}</option>)}
                </FormControl>
            </FormGroup>
            <FormGroup validationState={this.state.projectTemplateUrl === '' ? 'error' : null}>
                <ControlLabel>
                    Templates
                </ControlLabel>
                <FormControl componentClass="select"
                    onChange={this.onProjectTemplateSelected}
                    placeholder="select">
                    <option />
                    {this.props.projectTemplates.map(t =>
                        <option key={t.id} value={t.url}>{t.title}</option>)}
                </FormControl>
            </FormGroup>
            <FormGroup validationState={this.state.modules.length === 0 ? 'error' : null}>
                <ControlLabel>
                    Modules
                </ControlLabel>
                <StringMultipleSelect
                    items={this.props.modules}
                    title="Select modules"
                    selectedItems={this.state.modules}
                    onSelectionChanged={this.onModulesSelectionChanged} />
            </FormGroup>
            <FormGroup validationState={this.state.dueDateValid ? null : 'error'}>
                <ControlLabel>Due Date</ControlLabel>
                <DateTime
                    onUpdated={this.onDueDateUpdated}
                    initialDate={this.state.dueDateUtc}
                    timeFormat={Consts.TimeFormat}
                    dateFormat={Consts.DateFormat} />
            </FormGroup>
            <FormGroup>
                <ButtonToolbar>
                    <Button
                        className="pull-right"
                        disabled={!this.valid}
                        onClick={this.onAddModules}
                        bsStyle="success">
                            Add Modules
                    </Button>
                </ButtonToolbar>
            </FormGroup>
        </Form>;
    }
}


