import * as React from 'react';
import { ProgressBar } from 'react-bootstrap';
import { connect } from 'react-redux';

import modal from '../../components/Modal/Modal';
import { IApplicationState } from '../../store';

function mapStateToProps(state: IApplicationState) {
    return {
        visible: state.projectsDashboard.projectsRequest
            || state.common.allUsersRequest
            || state.addModulesFormState.addModulesRequest
            || state.addModulesFormState.projectTemplateDataRequest
            || state.addSingleTaskFormState.rolesByProjectRequest
            || state.addSingleTaskFormState.addSingleTaskRequest
            || state.projectsDashboard.projectDataRequest
            || state.updateTaskFormState.updateTaskRequest
            || state.teamDashboard.tasksRequest
            || state.teamDashboard.activeUsersRequest
            || state.resourceDashboard.eventsRequest
    }
}

const ProgressContainer: React.SFC<{visible: boolean}> = (props) => {
    if (props.visible) {
        const Component = modal(ProgressBar, { title: 'Loading...', hideCloseBtn: true });
        const progressBarProps = {
            active: true,
            bsStyle: 'info',
            now: 100,
            striped: true
        }
        return <Component {...progressBarProps} />
    } return null;
}

export default connect(mapStateToProps, {})(ProgressContainer);


