import SPLookupValue from './SPLookupValue';

export default class Project {
    public id: number = 0;
    public projectName: string = '';
    public deltekProjectNumber: string = '';
    public phases: SPLookupValue[] = [];

    public projectManager: SPLookupValue | null = null;
    public principal: SPLookupValue | null = null;

    public siteUrl: string = '';
    public profitCenter: string = '';
}


