import SPLookupValue from "./SPLookupValue";

export default class Role {
    public id: number = 0;

    public name: string = '';
    public users: SPLookupValue[] = [];

    public get usersStr(): string {
        return this.users.map(u => u.displayName).join(', ');
    }
}


