import { connect } from 'react-redux';

import actionCreators from '../../actions/RootModal/ActionCreators';
import EventsCalendar from '../../components/EventsCalendar/EventsCalendar/EventsCalendar';
import CalendarEvent from '../../models/CalendarEvent';
import EventType from '../../models/EventType';
import Milestone from '../../models/Milestone';
import Task from '../../models/Task';
import { IApplicationState } from '../../store';

const fromTasksToEvents = (tasks: Task[]): CalendarEvent[] => {
    return tasks
    .filter(t => t.dueLocal.isValid())
    .map(t => {
        const event = new CalendarEvent();
        event.type = EventType.Task;
        event.date = t.dueLocal.toDate();
        event.entity = t;

        return event;
    });
}

const fromMilestonesToEvents = (milestones: Milestone[]): CalendarEvent[] => {
    return milestones
    .filter(m => m.eventDateLocal.isValid())
    .map(m => {
        const event = new CalendarEvent();
        event.type = EventType.Milestone;
        event.date = m.eventDateLocal.toDate();
        event.entity = m;

        return event;
    });
}


const mapStateToProps = (state: IApplicationState) => {
    const eventsByTasks = fromTasksToEvents(state.resourceDashboard.tasks);
    const eventsByMilestones = fromMilestonesToEvents(state.resourceDashboard.milestones);

    return {
        events: eventsByTasks.concat(eventsByMilestones)
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        onTaskEdit: (task: Task) => dispatch(
            actionCreators.OpenModalDialog('EDIT_TASK', { task }, 'Update Task')),
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps)(EventsCalendar);


