import { Action, Reducer } from 'redux';

import ActionTypes from '../../actions/ActionTypes';
import * as Actions from '../../actions/ResourceDashboard/IActions';
import ResourceDashboardState from '../../store/Dashboards/ResourceDashboardState';

const initialState = {
    eventsRequest: false,
    eventsRequestError: false,
    milestones: [],
    tasks: [],

    filter: {
        offices: [],
        users: [],

        includeMilestones: false,
        includeTasks: false
    }
}

export const ResourceDashboardReducer: Reducer<ResourceDashboardState> = (state: ResourceDashboardState = initialState, incomingAction: Action) => {
    const action = incomingAction as Actions.RequestEventsAction
        | Actions.IResourceDashboardFilterUpdated
        | Actions.IResourceDashboardTasksUpdatedAction;

    switch (action.type) {
        case ActionTypes.REQUEST_EVENTS:
            {
                return {
                    ...state,
                    eventsRequest: true
                };
            }
        case ActionTypes.EVENTS_REQUEST_ERROR:
            {
                return {
                    ...state,
                    eventsRequest: false,
                    eventsRequestError: true
                };
            }
        case ActionTypes.RECEIVE_EVENTS:
            {
                return {
                    ...state,
                    eventsRequest: false,
                    eventsRequestError: false,
                    milestones: action.milestones,
                    tasks: action.tasks
                };
            }
        case ActionTypes.RESOURCE_DASHBOARD_TASKS_UPDATED:
            {
                return {
                    ...state,
                    tasks: action.tasks
                };
            }
        case ActionTypes.RESOURCE_DASHBOARD_FILTER_UPDATED: {
            return {
                ...state,
                filter: action.filter
            };
        }
        default: {
            return state;
        }
    }
};


